import { html } from 'lit-element';
import { getComponentSharedStyles } from '@cells-components/cells-lit-helpers';
import { CellsButton } from '@cells-components/cells-button/cells-button.js';
import styles from './bbva-button-icon-styles.js';
import '@cells-components/cells-icon/cells-icon.js';

export { bbvaButtonIconAmbient } from './bbva-button-icon-ambient.js';
/**
Icon buttons are smaller and simpler than default buttons. Made up of an icon instead of a label they simplify user actions to compliment other elements to generate full components.

Example:

```html
<bbva-button-icon icon="coronita:search">Accesible text</bbva-button-icon>
<bbva-button-icon icon="coronita:search">Accesible text</bbva-button-icon>
<bbva-button-icon icon="coronita:search" disabled>Accesible text</bbva-button-icon>
```

## Styling

### Custom Properties & Foundations Styles
|                 Selector                 |    CSS Property     |                   CSS Variable                    |          Theme Variable           |       Foundations/Fallback        |
| :--------------------------------------- | :------------------ | :------------------------------------------------ | :-------------------------------- | :-------------------------------- |
| :host                                    | width / height      | --bbva-button-icon-size                           |                                   | rem(48px))                        |
| :host                                    | background          | --bbva-button-icon-background                     | --colorsPrimaryMedium             | primaryMedium                     |
| :host                                    | color               | --bbva-button-icon-color                          | --colorsPrimaryCoreLightened      | primaryCoreLightened              |
| :host([focus-visible])                   | background          | --bbva-button-icon-background-focus               | --colorsPrimaryCoreLight          | primaryCoreLight                  |
| :host([focus-visible])                   | color               | --bbva-button-icon-color-focus                    | --colorsPrimaryCoreLightened      | primaryCoreLightened              |
| :host([disabled])                        | background          | --bbva-button-icon-background-disabled            | --colorsSecondary200              | secondary200                      |
| :host([disabled])                        | color               | --bbva-button-icon-color-disabled                 | --colorsSecondary400              | secondary400                      |


### @apply
|                 Selector                  |                       Mixin                        |
| :---------------------------------------- | :------------------------------------------------- |
| :host                                     | --bbva-button-icon                                 |
| :host([focus-visible])                    | --bbva-button-icon-focus                           |
| :host([disabled])                         | --bbva-button-icon-disabled                        |

@customElement bbva-button-icon
@polymer
@LitElement
@demo demo/index.html
*/
export class BbvaButtonIcon extends CellsButton {
  static get is() {
    return 'bbva-button-icon';
  }

  static get properties() {
    return {
      /**
       * Icon for the button
       */
      icon: {
        type: String,
      },
      /**
       * Button icon size
       * @default 24
       */
      iconSize: {
        type: String,
        attribute: 'icon-size',
      },
    };
  }

  constructor() {
    super();
    this.iconSize = 24;
  }

  static get shadyStyles() {
    return `
      ${super.shadyStyles}
      ${styles.cssText}
      ${getComponentSharedStyles('bbva-button-icon-shared-styles').cssText}
    `;
  }

  render() {
    return html`
      <style>
        ${this.constructor.shadyStyles}
      </style>
      <cells-icon .icon="${this.icon}" .size="${this._iconSize}"></cells-icon>
      ${this._button}
      <span>
        <slot></slot>
      </span>
    `;
  }
}

customElements.define(BbvaButtonIcon.is, BbvaButtonIcon);
