import { dedupingMixin, } from '@cells-components/cells-lit-helpers/utils/mixin.js';

/**
This mixin allows a custom element to "delegate" attributes from its main node to an inner node. These attributes won't be set on the main node, just in the inner node.

The component using this mixin must define a 'delegatedAttributesTarget' getter returning the target node, and a 'delegatedAttributes' getter returning an array which contains the attribute names to be delegated.

The mixin will check on 'firstUpdated' lifecycle callback for attributes defined in markup to pass them to the inner target.

```html
  class delegatedAttributesElement extends CellsDelegateAttributesMixin(LitElement) {
    get delegatedAttributes() {
      return [
        'delegated-attr'
      ];
    }
    get delegatedAttributesTarget() {
      return this._innerNode;
    }
    firstUpdated() {
      this._innerNode = this.shadowRoot.querySelector('.innerNode');
      super.firstUpdated();
    }
    render() {
      return html`
      <div class="innerNode"></div>
    `;
    }
  }
  customElements.define('delegated-attributes-element', delegatedAttributesElement);
```

An initial markup like this:
```html
<delegated-attributes-element delegated-attr="test-value"></delegated-attributes-element>
```

will output this:
```html
<delegated-attributes-element>
  #shadowRoot
    <div class="innerNode" delegated-attr="test-value"></div>
</delegated-attributes-element>
```

Adding delegated attributes using 'setAttribute' will set them in the inner node, not in the custom element. Delegated attributes can be removed from the inner node calling 'removeAttribute' on the custom element.

* @polymer
* @module CellsDelegateAttributesMixin
* @mixinFunction
*/
export const CellsDelegateAttributesMixin = dedupingMixin(Base => {
  return class CellsDelegateAttributes extends Base {

    get delegatedAttributes() {
      return [];
    }

    firstUpdated(changedProps) {
      super.firstUpdated(changedProps);
      this._initialAttributeDelegation();
    }

    /**
     * Overrides element own setAttribute method to perform attribute delegation
     * @param {String} name  Attribute name
     * @param {String} value Attribute value
     */
    setAttribute(name, value) {
      const attributeNames = this.delegatedAttributes;
      if (this.delegatedAttributesTarget && attributeNames.indexOf(name) > -1) {
        this.delegatedAttributesTarget.setAttribute(name, value);
        super.removeAttribute(name);
      } else {
        super.setAttribute(name, value);
      }
    }

    /**
     * Overrides element ow removeAttribute method to perform attribute delegation
     * @param {String} name  Attribute name
     */
    removeAttribute(name) {
      const attributeNames = this.delegatedAttributes;
      if (this.delegatedAttributesTarget && attributeNames.indexOf(name) > -1) {
        this.delegatedAttributesTarget.removeAttribute(name);
      }
      super.removeAttribute(name);
    }

    _initialAttributeDelegation() {
      if (this.delegatedAttributesTarget) {
        this.delegatedAttributes.forEach(attributeName => {
          const attributeValue = this.getAttribute(attributeName);
          if (typeof attributeValue === 'string') {
            this.delegatedAttributesTarget.setAttribute(attributeName, attributeValue);
            super.removeAttribute(attributeName);
          }
        });
      }
    }
  };
});
