import { LitElement, html } from 'lit-element';
import { getComponentSharedStyles } from '@cells-components/cells-lit-helpers';
import { ifDefined } from 'lit-html/directives/if-defined.js';
import styles from './bbva-badge-notification-styles.js';
/**
Badge of notifications. When it is red it is simply informative of the number of notifications you have pending. When it is green or blue it is associated with a panel action.

Available classes:
* abs: Absolute position of component. When use, add `position: relative` to the father. Extra classes:
  * top-left: Top left position. Translate -8px/-8px.
  * top-right: Top right position. Translate 8px/-8px.
  * bottom-left: Bottom left position. Translate -8px/8px.
  * bottom-right: Bottom right position. Translate 8px/8px.

Example:
```html
<bbva-badge-notification
  accessibility-text="8 Notificaciones"
  text="8"></bbva-badge-notification>

<bbva-badge-notification
  accessibility-text="8 Notificaciones"
  class="abs top-right"
  text="8"></bbva-badge-notification>
```

## Styling

The following custom properties and mixins are available for styling:

### Custom Properties & Foundations Styles
| Selector                                 | CSS Property     | CSS Variable                                       | Theme Variable                    | Foundations/Fallback              |
|:-----------------------------------------|:-----------------|:---------------------------------------------------|:----------------------------------|:----------------------------------|
| :host                                    | font-size        | --bbva-badge-notification-font-size                | --typographyType3XSmall           | typography.type3XSmall            |
| :host                                    | font-weight      | --bbva-badge-notification-font-weight              | --fontFacePrimaryMediumFontWeight | fontFacePrimary.medium.fontWeight |
| .badge-notification                      | background-color | --bbva-badge-notification-bg-color                 | --colorsBbvaDarkRed               | colors.bbvaDarkRed                |
| .badge-notification                      | border-color     | --bbva-badge-notification-border-color             | --colorsBbvaWhite                 | colors.bbvaWhite                  |
| .badge-notification                      | color            | --bbva-badge-notification-color                    | --colorsBbvaWhite                 | colors.bbvaWhite                  |
| :host(.border-dark ) .badge-notification | border-color     | --bbva-badge-notification-border-dark-border-color | --colorsBbvaCoreBlue              | colors.bbvaCoreBlue               |
| :host(.dark ) .badge-notification        | background-color | --bbva-badge-notification-dark-bg-color            | --colorsBbvaWhiteCoreBlue         | colors.bbvaWhiteCoreBlue          |
| :host(.abs)                              | position         | --bbva-badge-notification-position                 |                                   | absolute                          |

### @apply
| Selector             | Mixin                                  |
|:---------------------|:---------------------------------------|
| :host                | --bbva-badge-notification              |
| .badge-notification  | --bbva-badge-notification-span         |
| :host(.border-dark)  | --bbva-badge-notification-border-dark  |
| :host(.dark)         | --bbva-badge-notification-dark         |
| :host(.abs)          | --bbva-badge-notification-abs          |
| :host(.top-left)     | --bbva-badge-notification-top-left     |
| :host(.top-right)    | --bbva-badge-notification-top-right    |
| :host(.bottom-left)  | --bbva-badge-notification-bottom-left  |
| :host(.bottom-right) | --bbva-badge-notification-bottom-right |

@customElement bbva-badge-notification
@polymer
@LitElement
@demo demo/index.html
*/
class BbvaBadgeNotification extends LitElement {
  static get is() {
    return 'bbva-badge-notification';
  }

  static get properties() {
    return {
      /**
       * Accessibility text of the notification
       */
      accessibilityText: {
        type: String,
        attribute: 'accessibility-text',
      },
      /**
       * Visible text of the notification
       */
      text: {
        type: String,
      },
    };
  }

  static get shadyStyles() {
    return `
      ${styles.cssText}
      ${getComponentSharedStyles('bbva-badge-notification-shared-styles').cssText}
    `;
  }

  render() {
    return html`
      <style>
        ${this.constructor.shadyStyles}
      </style>
      <span class="badge-notification" aria-label=${ifDefined(this.accessibilityText)}
        >${this.text}</span
      >
    `;
  }
}

customElements.define(BbvaBadgeNotification.is, BbvaBadgeNotification);
