import { css, unsafeCSS } from 'lit-element';
import * as foundations from '@bbva-web-components/bbva-foundations-styles/bbva-foundations-styles.js';

export default css`:host {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: block;
  box-sizing: border-box;
  font-size: var(--bbva-badge-default-font-size, var(--typographyTypeSmall, ${unsafeCSS(foundations.typography.typeSmall)}));
  line-height: var(--bbva-badge-default-line-height, var(--lineHeightTypeSmall, ${unsafeCSS(foundations.lineHeight.typeSmall)}));
  font-weight: var(--bbva-badge-default-font-weight, var(--fontFacePrimaryBookItalicFontWeight, ${unsafeCSS(foundations.fontFacePrimary.bookItalic.fontWeight)}));
  font-style: var(--bbva-badge-default-font-style, var(--fontFacePrimaryBookItalicFontStyle, ${unsafeCSS(foundations.fontFacePrimary.bookItalic.fontStyle)}));
  color: var(--bbva-badge-default-color, var(--colorsPrimaryMedium, ${unsafeCSS(foundations.colors.primaryMedium)}));
  @apply --bbva-badge-default; }

:host([hidden]), [hidden] {
  display: none !important; }

*, *:before, *:after {
  box-sizing: inherit; }

:host(.error) {
  color: var(--bbva-badge-default-error-color, var(--colorsTertiaryType1Dark, ${unsafeCSS(foundations.colors.tertiaryType1Dark)})); }

:host(.neutral) {
  color: var(--bbva-badge-default-neutral-color, var(--colorsSecondary500, ${unsafeCSS(foundations.colors.secondary500)})); }

:host(.success) {
  color: var(--bbva-badge-default-success-color, var(--colorsTertiaryType6Dark, ${unsafeCSS(foundations.colors.tertiaryType6Dark)})); }

:host(.warning) {
  color: var(--bbva-badge-default-warning-color, var(--colorsTertiaryType2Dark, ${unsafeCSS(foundations.colors.tertiaryType2Dark)})); }

:host(.alt) {
  color: var(--bbva-badge-default-alt-color, var(--colorsPrimaryVariantDark, ${unsafeCSS(foundations.colors.primaryVariantDark)})); }
`;