import { css, unsafeCSS } from 'lit-element';
import * as foundations from '@bbva-web-components/bbva-foundations-styles';

export default css`:host {
  --_control-color: var(--bbva-form-toggle-control-color, var(--colorsSecondary500, ${unsafeCSS(foundations.colors.secondary500)}));
  --_control-checked-color: var(--bbva-form-toggle-control-checked-color, var(--colorsPrimaryMedium, ${unsafeCSS(foundations.colors.primaryMedium)}));
  --_knob-tie-color: var(--bbva-form-toggle-knob-tie-color, var(--colorsPrimaryCoreLightened, ${unsafeCSS(foundations.colors.primaryCoreLightened)}));
  --_knob-tie-checked-color: var(--bbva-form-toggle-knob-tie-checked-color, var(--colorsPrimaryCoreLightened, ${unsafeCSS(foundations.colors.primaryCoreLightened)}));
  --_knob-tag-checked: var(--bbva-form-toggle-knob-tag-checked-color, var(--colorsPrimaryMedium, ${unsafeCSS(foundations.colors.primaryMedium)}));
  --_knob-tag-unchecked: var(--bbva-form-toggle-knob-tag-unchecked-color, var(--colorsPrimaryCoreLightened, ${unsafeCSS(foundations.colors.primaryCoreLightened)}));
  --_label-color: var(--bbva-form-toggle-label-color, var(--colorsSecondary600, ${unsafeCSS(foundations.colors.secondary600)}));
  --_knob-width: var(--bbva-form-toggle-control-width, 2.5rem);
  --_knob-height: var(--bbva-form-toggle-control-height, 1.5rem);
  --_knob-margin: var(--bbva-form-toggle-control-border, 1px);
  --_knob-tie-width: var(--bbva-form-toggle-control-tie-width, 1.375rem);
  display: inline-block;
  position: relative;
  font: inherit;
  box-sizing: border-box;
  cursor: pointer;
  font-size: var(--typographyTypeSmall, ${unsafeCSS(foundations.typography.typeSmall)});
  line-height: var(--lineHeightTypeSmall, ${unsafeCSS(foundations.lineHeight.typeSmall)});
  font-weight: var(--fontFacePrimaryBookFontWeight, ${unsafeCSS(foundations.fontFacePrimary.book.fontWeight)});
  color: var(--_label-color);
  @apply --bbva-form-toggle; }

:host([hidden]), [hidden] {
  display: none !important; }

*, *:before, *:after {
  box-sizing: inherit; }

.sr-only {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

:host([aria-disabled=true]) {
  opacity: .4;
  cursor: default;
  pointer-events: none;
  @apply --bbva-form-toggle-disabled; }

:host([radio]) {
  --_knob-width: var(--bbva-form-toggle-radio-control-width, 4.6875rem);
  --_knob-tie-width: var(--bbva-form-toggle-radio-control-tie-width, 2.25rem);
  @apply --bbva-form-toggle-radio; }

:host([radio][variant=long]) {
  --_knob-width: var(--bbva-form-toggle-radio-long-control-width, 5.5rem);
  --_knob-tie-width: var(--bbva-form-toggle-radio-long-control-tie-width, 2.6875rem);
  @apply --bbva-form-toggle-radio-long; }

:host([variant^=list]),
:host([variant=section]) {
  display: block;
  @apply --bbva-form-toggle-variant; }

.container {
  display: flex;
  align-items: center;
  padding: var(--bbva-form-toggle-container-padding, calc(((1 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 16) * 1rem) 0);
  @apply --bbva-form-toggle-container; }
  :host([variant^=list]) .container {
    padding: var(--bbva-form-toggle-list-container-padding, calc(((1.5 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 16) * 1rem) 0);
    @apply --bbva-form-toggle-list-container; }
  :host([variant=section]) .container {
    padding: var(--bbva-form-toggle-section-container-padding, calc(((3 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 16) * 1rem) 0);
    @apply --bbva-form-toggle-section-container; }

.bg {
  flex: none;
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  height: var(--_knob-height);
  width: var(--_knob-width);
  background-color: var(--_control-color);
  border-radius: 0.8125rem;
  overflow: hidden;
  @apply --bbva-form-toggle-control; }
  :host([radio]) .bg {
    background-color: var(--_control-checked-color);
    @apply --bbva-form-toggle-radio-control; }
  :host([checked]) .bg {
    background-color: var(--_control-checked-color);
    @apply --bbva-form-toggle-checked-control; }
  :host([variant=list-right]) .bg,
  :host([variant=section]) .bg {
    order: 1;
    @apply --bbva-form-toggle-right-control; }

.knob {
  position: absolute;
  top: var(--_knob-margin);
  left: var(--_knob-margin);
  right: var(--_knob-margin);
  bottom: var(--_knob-margin);
  transition: transform .2s;
  @apply --bbva-form-toggle-knob; }
  :host([checked]) .knob {
    transform: translateX(calc(100% - var(--_knob-tie-width)));
    @apply --bbva-form-toggle-checked-knob; }
  .knob-tie {
    display: block;
    height: 100%;
    width: var(--_knob-tie-width);
    border-radius: 0.8125rem;
    background-color: var(--_knob-tie-color);
    @apply --bbva-form-toggle-knob-tie; }
    :host([radio]) .knob-tie {
      background-color: var(--_knob-tie-checked-color);
      @apply --bbva-form-toggle-radio-knob-tie; }
    :host([checked]) .knob-tie {
      background-color: var(--_knob-tie-checked-color);
      @apply --bbva-form-toggle-checked-knob-tie; }
  .knob-tag {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1px;
    width: 50%;
    font-size: var(--typographyType2XSmall, ${unsafeCSS(foundations.typography.type2XSmall)});
    line-height: var(--lineHeightType2XSmall, ${unsafeCSS(foundations.lineHeight.type2XSmall)});
    font-weight: var(--fontFacePrimaryMediumFontWeight, ${unsafeCSS(foundations.fontFacePrimary.medium.fontWeight)});
    @apply --bbva-form-toggle-knob-tag; }
    .knob-tag,
    :host([checked]) .knob-tag ~ .knob-tag {
      color: var(--_knob-tag-checked);
      @apply --bbva-form-toggle-checked-knob-tag; }
    :host([checked]) .knob-tag,
    .knob-tag ~ .knob-tag {
      color: var(--_knob-tag-unchecked);
      @apply --bbva-form-toggle-unchecked-knob-tag; }

.label {
  flex: auto;
  display: flex;
  align-items: center;
  min-width: 0;
  @apply --bbva-form-toggle-label; }
  :host([variant=list]) .label {
    margin-left: calc(((1 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 16) * 1rem);
    @apply --bbva-form-toggle-list-label; }
  :host([variant=list-right]) .label,
  :host([variant=section]) .label {
    margin-right: calc(((1 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 16) * 1rem);
    @apply --bbva-form-toggle-right-label; }
  :host([variant=section]) .label {
    font-weight: var(--fontFacePrimaryMediumFontWeight, ${unsafeCSS(foundations.fontFacePrimary.medium.fontWeight)});
    @apply --bbva-form-toggle-section-label; }
  .label-text {
    flex: auto;
    display: block;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    @apply --bbva-form-toggle-label-text; }
  .label-description {
    flex: none;
    font-size: var(--typographyType3XSmall, ${unsafeCSS(foundations.typography.type3XSmall)});
    line-height: var(--lineHeightType3XSmall, ${unsafeCSS(foundations.lineHeight.type3XSmall)});
    display: block;
    margin-left: auto;
    @apply --bbva-form-toggle-label-description; }

.slot ::slotted(*) {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin: 0;
  opacity: 0;
  z-index: -1;
  pointer-events: none; }
`;