import { css, unsafeCSS } from 'lit-element';
import * as foundations from '@bbva-web-components/bbva-foundations-styles/bbva-foundations-styles.js';

export default css`:host {
  --_bg-color: var(--bbva-badge-notification-bg-color, var(--colorsTertiaryType1Dark, ${unsafeCSS(foundations.colors.tertiaryType1Dark)}));
  --_border-color: var(--bbva-badge-notification-border-color, var(--colorsPrimaryCoreLightened, ${unsafeCSS(foundations.colors.primaryCoreLightened)}));
  --_color: var(--bbva-badge-notification-color, var(--colorsPrimaryCoreLightened, ${unsafeCSS(foundations.colors.primaryCoreLightened)}));
  display: block;
  box-sizing: border-box;
  font-size: var(--bbva-badge-notification-font-size, var(--typographyType3XSmall, ${unsafeCSS(foundations.typography.type3XSmall)}));
  font-weight: var(--bbva-badge-notification-font-weight, var(--fontFacePrimaryMediumFontWeight, ${unsafeCSS(foundations.fontFacePrimary.medium.fontWeight)}));
  @apply --bbva-badge-notification; }

:host([hidden]), [hidden] {
  display: none !important; }

*, *:before, *:after {
  box-sizing: inherit; }

.badge-notification {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  min-width: calc(((3 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 12) * 1em);
  height: calc(((3 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 12) * 1em);
  padding: 0 calc(((0.5 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 12) * 1em);
  border-radius: 50%;
  background-color: var(--_bg-color);
  border: 1px solid var(--_border-color);
  color: var(--_color);
  @apply --bbva-badge-notification-span; }

:host(.border-dark) {
  --_border-color: var(--bbva-badge-notification-border-dark-border-color, var(--colorsPrimaryCore, ${unsafeCSS(foundations.colors.primaryCore)}));
  @apply --bbva-badge-notification-border-dark; }

:host(.dark) {
  --_bg-color: var(--bbva-badge-notification-dark-bg-color, var(--colorsPrimaryCoreLight, ${unsafeCSS(foundations.colors.primaryCoreLight)}));
  @apply --bbva-badge-notification-dark; }

:host(.abs) {
  position: var(--bbva-badge-notification-position, absolute);
  @apply --bbva-badge-notification-abs; }

:host(.top-left) {
  bottom: 100%;
  right: 100%;
  transform: translate(1rem, 1rem);
  @apply --bbva-badge-notification-top-left; }

:host(.top-right) {
  bottom: 100%;
  left: 100%;
  transform: translate(-1rem, 1rem);
  @apply --bbva-badge-notification-top-right; }

:host(.bottom-left) {
  top: 100%;
  right: 100%;
  transform: translate(1rem, -1rem);
  @apply --bbva-badge-notification-bottom-left; }

:host(.bottom-right) {
  top: 100%;
  left: 100%;
  transform: translate(-1rem, -1rem);
  @apply --bbva-badge-notification-bottom-right; }
`;