/**

`cells-iron-meta` is based on Polymer's `iron-meta` is a generic element you can use for sharing information across the
DOM tree. It uses [monostate pattern](http://c2.com/cgi/wiki?MonostatePattern)
such that any instance of iron-meta has access to the shared information.
You can use `cells-iron-meta` to share whatever you want.

The `cells-iron-meta` instances containing your actual data.
The only requirement is that you create them before you try to access them.

Examples:


*/
export class CellsIronMeta {
  /**
   * @param {{
   *   type: (string|null|undefined),
   *   key: (string|null|undefined),
   *   value: *,
   * }=} options
   */
  constructor(options) {
    this.type = (options && options.type) || 'default';
    this.key = (options && options.key);
    this.constructor.classFieldTypes(); //https://developers.google.com/web/updates/2018/12/class-fields
    if (options && 'value' in options) {
      this.value = options.value;
    }
  }

  static classFieldTypes() {
    if (this.types) {
      return;
    }
    this.types = {};
  }

  /** @return {*} */
  get value() {
    let type = this.type;
    let key = this._key ? this._key : this.key;
    this._key = undefined;
    if (type && key) {
      return this.constructor.types[type] && this.constructor.types[type][key];
    }
    return undefined;
  }

  /** @param {*} value */
  set value(value) {
    let type = this.type;
    let key = this.key;

    if (type && key) {
      type = this.constructor.types[type] = this.constructor.types[type] || {};

      if (value === null) {
        delete type[key];
      } else {
        type[key] = value;
      }
    }
  }

  /** @return {!Array<*>} */
  get list() {
    const type = this.type;
    if (type) {
      const itemsType = this.constructor.types[type];
      if (itemsType) {
        return Object.keys(itemsType).map((itemType) => itemsType[itemType], this);
      }
      return [];
    }
    return undefined;
  }

  /**
   * @param {string} key
   * @return {*}
   */
  byKey(key) {
    this._key = key;
    return this.value;
  }
}

