import { html } from 'lit-element';
import { ifDefined } from 'lit-html/directives/if-defined.js';
import { getComponentSharedStyles } from '@cells-components/cells-lit-helpers';
import { BbvaFormField } from '@bbva-web-components/bbva-form-field';
import '@bbva-web-components/bbva-clip-flag';
import styles from './bbva-form-text-styles.js';

export { bbvaFormFieldAmbient } from '@bbva-web-components/bbva-form-field';
/**
Text fields enable the user to input or interact with text in an application.

This component extends 'BbvaFormField' to get input field styling & functionality, and exports the same ambients. It allows to add an icon flag in the field. When using a flag, an accessible label flag should always be provided.

**Example:**
```html
<bbva-form-text label="Field label"></bbva-form-text>
<bbva-form-text label="Field label" flag-label="Spain" flag="es"></bbva-form-text>
<bbva-form-text label="Field label" value="Initial value" required></bbva-form-text>
<bbva-form-text label="Field label" value="Initial value" disabled></bbva-form-text>
<bbva-form-text label="Field label" value="Initial value" autofocus></bbva-form-text>
<bbva-form-text
  label="Field label"
  value="Initial value"
  allowed-pattern="[0-9]"
  auto-validate
  pattern="\d{3}"
  form="formId"
></bbva-form-text>
<bbva-form-text
  label="Field label"
  value="Initial value"
  allowed-pattern="[0-9]"
  validate-on-blur
  pattern="\d{3}"
></bbva-form-text>
```

## Icons

Since this component uses icons, it will need an [iconset](https://platform.bbva.com/en-us/developers/engines/cells/documentation/cells-architecture/components/components-in-depth/icons) in your project as an application level dependency. In fact, this component uses an iconset in its demo.

## Styling

The following mixins are available for styling, as well as all custom properties and mixins from 'BbvaFormField':

### @apply

| Selector                    | Mixin                            |
|:----------------------------|:---------------------------------|
| :host                       | --bbva-form-text                 |
| .with-flag ::slotted(input) | --bbva-form-text-with-flag-input |
| .with-flag .label           | --bbva-form-text-with-flag-label |
| .flag                       | --bbva-form-text-flag            |

@customElement bbva-form-text
@polymer
@LitElement
@demo demo/index.html
*/
export class BbvaFormText extends BbvaFormField {
  static get is() {
    return 'bbva-form-text';
  }

  static get properties() {
    return {
      /**
       * Flag image size
       * Values: "small", "medium" or "large"
       */
      flagSize: {
        type: String,
        attribute: 'flag-size',
      },

      /**
       * Flag icon, not containing iconset name
       */
      flag: {
        type: String,
      },

      /**
       * Flags iconset name
       */
      flagIconset: {
        type: String,
        attribute: 'flag-iconset',
      },

      /**
       * Flag name to be added to input accessible label
       */
      flagLabel: {
        type: String,
        attribute: 'flag-label',
      },
    };
  }

  constructor() {
    super();
    this.type = 'text';
    this.flagSize = 'small';
    this.flagText = '';
  }

  static get shadyStyles() {
    return `
      ${super.shadyStyles}
      ${styles.cssText}
      ${getComponentSharedStyles('bbva-form-text-shared-styles').cssText}
    `;
  }

  /**
   * Layer containing flag block, label, input and field buttons with listeners for focusin and focusout
   * @return {TemplateResult} HTML of field containing flag block, input, label and clear button
   */
  get field() {
    return html`
      <div
        class="field ${this.flag ? 'with-flag' : ''}"
        @focusin="${this._onFocusin}"
        @focusout="${this._onFocusout}"
      >
        ${this.flagBlock} ${this.labelledInput} ${this.clearButton}
      </div>
    `;
  }

  /**
   * HTML block containing flag
   * @return {TemplateResult} HTML with flag
   */
  get flagBlock() {
    return html`
      ${this.flag
        ? html`
            <span class="flag">
              <bbva-clip-flag
                icon="${ifDefined(this.flag)}"
                .size="${this.flagSize}"
                iconset="${ifDefined(this.flagIconset)}"
              ></bbva-clip-flag>
            </span>
          `
        : ''}
    `;
  }

  /**
   * HTML block containing label span
   * @return {TemplateResult} HTML with label
   */
  get labelBlock() {
    return html`
      <span class="label" aria-hidden="true" @click="${this.focus}">${this.label}</span>
    `;
  }

  /**
   * Returns text to be used as input aria-label, joining flag text if available
   * @return {String} aria-label text for input
   */
  get inputLabel() {
    return `${this.label}${this.flagLabel ? ` - ${this.flagLabel}` : ''}`;
  }
}

customElements.define(BbvaFormText.is, BbvaFormText);
