import { css, unsafeCSS } from 'lit-element';
import * as foundations from '@bbva-web-components/bbva-foundations-styles';

export default css`[ambient^=dark] {
  --bbva-button-icon-background: var(--colorsPrimaryMedium, ${unsafeCSS(foundations.colors.primaryMedium)});
  --bbva-button-icon-color: var(--colorsPrimaryCoreLightened, ${unsafeCSS(foundations.colors.primaryCoreLightened)});
  --bbva-button-icon-background-focus: var(--colorsPrimaryCoreLight, ${unsafeCSS(foundations.colors.primaryCoreLight)});
  --bbva-button-icon-color-focus: var(--colorsPrimaryCoreLightened, ${unsafeCSS(foundations.colors.primaryCoreLightened)});
  --bbva-button-icon-background-disabled: var(--colorsSecondary200, ${unsafeCSS(foundations.colors.secondary200)});
  --bbva-button-icon-color-disabled: var(--colorsSecondary400, ${unsafeCSS(foundations.colors.secondary400)}); }

[ambient=dark100],
[ambient=dark150] {
  --bbva-button-icon-background: var(--colorsPrimaryCore, ${unsafeCSS(foundations.colors.primaryCore)}); }
`;
