import { css, unsafeCSS } from 'lit-element';
import * as foundations from '@bbva-web-components/bbva-foundations-styles';

export default css`:host {
  display: block;
  box-sizing: border-box;
  @apply --bbva-form-date; }

:host([hidden]), [hidden] {
  display: none !important; }

*, *:before, *:after {
  box-sizing: inherit; }

.slot-input ::slotted(input) {
  padding-right: calc(((6 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 15) * 1em);
  @apply --bbva-form-date-input; }
  :host([focused]) .slot-input ::slotted(input),
  :host([invalid][filled]) .slot-input ::slotted(input) {
    max-width: 100%;
    @apply --bbva-form-date-focused-input; }
  :host(:not([filled])) .slot-input ::slotted(input) {
    opacity: 0;
    @apply --bbva-form-date-empty-input; }
  :host([readonly][filled]) .slot-input ::slotted(input),
  :host([invalid][filled]) .slot-input ::slotted(input),
  :host([focused]) .slot-input ::slotted(input) {
    opacity: 1;
    @apply --bbva-form-date-visible-input; }

.clear {
  display: flex;
  justify-content: center;
  align-items: center;
  width: calc(((6 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 16) * 1rem);
  height: calc(((6 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 16) * 1rem);
  @apply --bbva-form-date-icon; }
`;