import { dedupingMixin } from '@cells-components/cells-lit-helpers/utils/mixin.js';
import { CellsIronMeta } from '@cells-components/cells-iron-meta/cells-iron-meta.js';

/**
 * Singleton CellsIronMeta instance.
 */
export let CellsValidatableMixinMeta = null;

/**
  Use `CellsValidatableMixin` to implement an element that validates user input. By using this behaviour, your custom element will get a public `validate()` method, which
  will return the validity of the element, and a corresponding `invalid` attribute,
  which can be used for styling. Can be used alongside an element implementing
  the  `CellsValidatableMixin` behaviour.

  It's directly based on Polymer Iron Validatable Behavior, see: [Documentation](https://www.webcomponents.org/element/@polymer/iron-validatable-behavior),
    [Demo](https://www.webcomponents.org/element/@polymer/iron-validatable-behavior/demo/demo/index.html).

  Usage examples:
  ```js
  class SampleElement extends CellsValidatableMixin(LitElement) {
    render() {
      return html`
        <style>
          :host {
            border: 1px solid green;
            color: green;
          }
          :host([invalid]) {
            border: 1px solid red;
            color: red;
          }
        </style>
        <input id="input">
      `;

      // Override this method if you want to implement custom validity
      // for your element. This element is only valid if the value in the
      // input is "cat".
      function _getValidity() {
        return this.$.input.value === 'cat';
      }
    }
  }
  customElements.define('sample-element', SampleElement);
  ```

  In an html file using the element:
  ```html
  <sample-element id="el"></sample-element>
  <button onclick="el.validate()">Validate!</button>
  ```
 * @polymer
 * @demo demo/index.html
 * @module CellsValidatableMixin
 * @mixinFunction
 */
export const CellsValidatableMixin = dedupingMixin(superClass => {
  return class extends superClass {
    static get properties() {
      return {
        /**
         * Name of the validator to use.
         */
        validator: {
          type: String
        },

        /**
         * True if the last call to `validate` is invalid.
         */
        invalid: {
          type: Boolean,
          reflect: true
        }
      };
    }

    constructor() {
      super();
      this.invalid = false;
      CellsValidatableMixinMeta = new CellsIronMeta({type: 'validator'});
    }

    updated(changedProps) {
      super.updated(changedProps);
      if (changedProps.has('invalid')) {
        this._invalidChanged();
        this.dispatchEvent(new CustomEvent('invalid-changed', {
          detail: {
            value: this.invalid
          }
        }));
      }
    }

    /* Recompute this every time it's needed, because we don't know if the
     * underlying CellsValidatableMixinMeta has changed. */
    get _validator() {
      return CellsValidatableMixinMeta &&
          CellsValidatableMixinMeta.byKey(this.validator);
    }

    _invalidChanged() {
      if (this.invalid) {
        this.setAttribute('aria-invalid', 'true');
      } else {
        this.removeAttribute('aria-invalid');
      }
    }

    /* eslint-disable no-eq-null, eqeqeq */
    /**
     * @return {boolean} True if the validator `validator` exists.
     */
    hasValidator() {
      return this._validator != null;
    }
    /* eslint-enable no-eq-null, eqeqeq */

    /**
     * Returns true if the `value` is valid, and updates `invalid`. If you want
     * your element to have custom validation logic, do not override this method;
     * override `_getValidity(value)` instead.

     * @param {Object} value Deprecated: The value to be validated. By default,
     * it is passed to the validator's `validate()` function, if a validator is
     set.
     * If this argument is not specified, then the element's `value` property
     * is used, if it exists.
     * @return {boolean} True if `value` is valid.
     */
    validate(value) {
      // If this is an element that also has a value property, and there was
      // no explicit value argument passed, use the element's property instead.
      if (value === undefined && this.value !== undefined) {
        this.invalid = !this._getValidity(this.value);
      } else {
        this.invalid = !this._getValidity(value);
      }
      return !this.invalid;
    }

    /**
     * Returns true if `value` is valid.  By default, it is passed
     * to the validator's `validate()` function, if a validator is set. You
     * should override this method if you want to implement custom validity
     * logic for your element.
     *
     * @param {Object} value The value to be validated.
     * @return {boolean} True if `value` is valid.
     */
    _getValidity(value) {
      if (this.hasValidator()) {
        return this._validator.validate(value);
      }
      return true;
    }

  };
});
