import { html } from 'lit-element';
import { getComponentSharedStyles } from '@cells-components/cells-lit-helpers';
import { CellsButton } from '@cells-components/cells-button/cells-button.js';
import { ifDefined } from 'lit-html/directives/if-defined.js';
import '@cells-components/cells-icon';

import styles from './bbva-link-styles.js';

/**
A link helps a user navigate to subpages within an application. It can include an icon and text elements.
Links are used primarily as a navigational element, to embed actions or pathways, or to view additional information.
They are used to reduce visual complexity of a design. With links, users can navigate to other pages,
windows, help information, start a command, and more.
Example:

```html
Button link
<bbva-link>Active</bbva-link>

Disabled button link
<bbva-link disabled>Active</bbva-link>

Anchor Link
<bbva-link href="https://www.bbva.com" target="_blank"></bbva-link>


## Styling

### Custom Properties & Foundations Styles
|                 Selector                 |    CSS Property     |             CSS Variable              |          Theme Variable           |       Foundations/Fallback        |
| :--------------------------------------- | :------------------ | :-------------------------------------| :-------------------------------- | :-------------------------------- |
| :host                                    | color               | --bbva-link-color                     | --colorsPrimaryMedium             | colors.primaryMedium
| :host([disabled])                        | opacity             | --bbva-link-disabled-opacity          |                                   | .4
| :host([disabled])                        | color               | --bbva-link-disabled-color            | --colorsSecondary400              | colors.secondary400
| :host([active])                          | color               | --bbva-link-active-color              | --colorsPrimaryCore               | colors.primaryCoreDark


### @apply
|                 Selector                  |                       Mixin                        |
| :---------------------------------------- | :------------------------------------------------- |
| :host                                     | --bbva-link                                        |
| :host([disabled])                         | --bbva-link-disabled                               |
| :host([active])                           | --bbva-link-active                                 |
| :host([icon])                             | --bbva-link-with-icon                              |
| :host .icon                               | --bbva-link-icon                                   |

```
@customElement bbva-link
@polymer
@LitElement
@demo demo/index.html
*/
export class BbvaLink extends CellsButton {
  static get is() {
    return 'bbva-link';
  }

  static get properties() {
    return {
      /**
       * Mark link as disabled
       */
      disabled: {
        type: Boolean,
        reflect: true,
      },
      /**
       * Icon to be shown
       */
      icon: {
        type: String,
      },
      /**
       * Icon size
       * @default 16
       */
      iconSize: {
        type: Number,
        attribute: 'icon-size',
      },
      /**
       * Href of the hidden link
       */
      href: {
        type: String,
      },
      /**
       * target of the hidden link
       */
      target: {
        type: String,
      },
      /**
       * Download attribute for link
       */
      download: {
        type: String,
      },
      /**
       * Hreflang attribute for link
       */
      hreflang: {
        type: String,
      },
      /**
       * rel attribute for link
       */
      rel: {
        type: String,
      },
      /**
       * referrerpolicy attribute for link
       */
      referrerpolicy: {
        type: String,
      },
    };
  }

  constructor() {
    super();
    this.iconSize = 16;
  }

  connectedCallback() {
    if (super.connectedCallback) {
      super.connectedCallback();
    }
    if (this.href) {
      this.setAttribute('role', 'link');
    }
  }

  static get shadyStyles() {
    return `
      ${super.shadyStyles}
      ${styles.cssText}
      ${getComponentSharedStyles('bbva-link-shared-styles').cssText}
    `;
  }

  get _iconTemplate() {
    return this.icon
      ? html`
          <cells-icon class="icon" .icon="${this.icon}" .size="${this.iconSize}"></cells-icon>
        `
      : html``;
  }

  get _button() {
    return this.href
      ? html`
          <a
            href="${this.href}"
            hreflang="${ifDefined(this.hreflang)}"
            target="${ifDefined(this.target)}"
            download="${ifDefined(this.download)}"
            referrerpolicy="${ifDefined(this.referrerpolicy)}"
            rel="${ifDefined(this.rel)}"
            style="display: none;"
            id="${this._formElementId}"
            aria-hidden="true"
            tabindex="-1"
          >
          </a>
        `
      : super._button;
  }

  render() {
    return html`
      <style>
        ${this.constructor.shadyStyles}
      </style>
      ${this._iconTemplate}
      <slot></slot>
      ${this._button}
    `;
  }
}

customElements.define(BbvaLink.is, BbvaLink);
