import { LitElement, html } from 'lit-element';
import { getComponentSharedStyles } from '@cells-components/cells-lit-helpers';
import { CellsI18nMixin as cellsI18nMixin } from '@cells-components/cells-i18n-mixin';
import '@cells-components/cells-heading';
import styles from './bbva-expandable-step-styles.js';

/**
`<bbva-expandable-multistep>` displays a step in a multistep component. It can be `active`, `disabled` or `completed`.

The default content (form to edit) should be placed in the default slot (without name). The result of the step should be placed in a slot named `summary`.

The current step number (`step`) and the total of steps (`totalSteps`) are automatically set from the parent multistep component.

Example:

```html
<bbva-expandable-step header="Step 1">
  <div class="content">
    <button>Continue</button>
  </div>

  <div class="summary" slot="summary">
    <p>Completed content</p>
  </div>
</bbva-expandable-step>
```

## Prefilled steps
To mark a step as prefilled (completed and not editable), add the boolean `prefilled` attribute:

```html
<bbva-expandable-step header="Step 1" prefilled>
  <div class="content">
    <button>Continue</button>
  </div>
</bbva-expandable-step>
```

## Completing a step

To mark a step as completed, an element in its Light DOM should fire an event (`completed` by default) that will set the edited step as `completed`.
The event name can be configured by setting `completeOnEvent` property.

The event should bubble.

Example with custom event name to complete steps:
```html
<bbva-expandable-multistep>
  <bbva-expandable-step header="Step 1" complete-on-event="custom-event-name">
    <div class="content">
      <button>Continue</button>
    </div>

    <div class="summary" slot="summary">
      <p>Completed content</p>
    </div>
  </bbva-expandable-step>
</bbva-expandable-multistep>

<script>
const button = document.querySelector('button');

button.addEventListener('click', function() {
  this.dispatchEvent(new CustomEvent('custom-event-name', {
    bubbles: true
  }));
});
</script>
```

## Disabling content in light DOM

To set an element in `<bbva-expandable-step>` light DOM as disabled while the step is `disabled`, set the `disabled-target` attribute with a CSS selector.
The attribute set in the `disabled-target` element is `disabled` by default, but it can be changed using `disabled-target-attr`.

Example using `disabled` attribute:
```html
<bbva-expandable-multistep>
  <bbva-expandable-step disabled-target=".action">
    <div class="content">
      <button class="action">Continue</button>
    </div>
  </bbva-expandable-step>
</bbva-expandable-multistep>
```

Example using custom attribute for the disabled element:
```html
<bbva-expandable-multistep>
  <bbva-expandable-step disabled-target=".action" disabled-target-attr="not-enabled">
    <div class="content">
      <button-component class="action">Continue</button-component>
    </div>
  </bbva-expandable-step>
</bbva-expandable-multistep>
```

## Styling

The following custom properties and mixins are available for styling:

### Custom Properties
| Custom Property                                    | Selector                                       | CSS Property     | Value                                                             |
| -------------------------------------------------- | ---------------------------------------------- | ---------------- | ----------------------------------------------------------------- |
| --bbva-expandable-step-background-color            | :host                                          | background-color | --bbva-white                                                      |
| --bbva-expandable-step-completed-background-color  | :host([completed])                             | background-color | --bbva-core-blue                                                  |
| --bbva-expandable-step-completed-color             | :host([completed])                             | color            | --bbva-white                                                      |
| --bbva-expandable-step-completed-step-border-color | :host([completed]:not(:first-of-type)) .header | border-top-color | --bbva-dark-medium-blue                                           |
| --bbva-expandable-step-disabled-opacity            | :host([disabled]) ::slotted(*)                 | opacity          |  0.5                                                              |
| --bbva-expandable-step-prefilled-background-color  | :host([prefilled])                             | background-color | --bbva-navy                                                       |
| --bbva-expandable-step-prefilled-color             | :host([prefilled])                             | color            | --bbva-white                                                      |
| --bbva-expandable-step-current-step-color          | .current-step                                  | color            | --bbva-500                                                        |
| --bbva-expandable-step-button-edit-color           | .btn-edit                                      | color            | --bbva-light-blue                                                 |
| --bbva-expandable-step-content-max-height          | .default-content                               | max-height       | 1000px                                                            |
| --bbva-expandable-step-default-content-max-height   | .default-content                              | max-height       | --bbva-expandable-step-content-max-height                         |
| --bbva-expandable-step-completed-content-max-height | :host([completed]) .completed-content         | max-height       | --bbva-expandable-step-content-max-height                         |

### @apply
| Mixins                                             | Selector                                       | Value |
| -------------------------------------------------- | ---------------------------------------------- | ----- |
| --bbva-expandable-step                             | :host                                          | {}    |
| --bbva-expandable-step-angle-border                | :host::before                                  | {}    |
| --bbva-expandable-step-default-content             | .default-content                               | {}    |
| --bbva-expandable-step-completed-content           | .completed-content                             | {}    |
| --bbva-expandable-step-completed                   | :host([completed])                             | {}    |
| --bbva-expandable-step-completed-completed-content | :host([completed]) .completed-content          | {}    |
| --bbva-expandable-step-completed-default-content   | :host([completed]) .default-content            | {}    |
| --bbva-expandable-step-completed-header            | :host([completed]:not(:first-of-type)) .header | {}    |
| --bbva-expandable-step-disabled                    | :host([disabled])                              | {}    |
| --bbva-expandable-step-active-angle-border         | :host([active]:not(:first-of-type))::before    | {}    |
| --bbva-expandable-step-prefilled                   | :host([prefilled])                             | {}    |
| --bbva-expandable-step-header                      | .header                                        | {}    |
| --bbva-expandable-step-heading                     | .heading                                       | {}    |
| --bbva-expandable-step-heading-label               | .heading-label                                 | {}    |
| --bbva-expandable-step-current-step                | .current-step                                  | {}    |
| --bbva-expandable-step-button-edit                 | .btn-edit                                      | {}    |
| --bbva-expandable-step-button-edit-active          | .btn-edit:active, .btn-edit:focus              | {}    |

@customElement bbva-expandable-step
@polymer
@LitElement
@demo demo/index.html
@appliesMixin CellsI18nMixin
*/

class BbvaExpandableStep extends cellsI18nMixin(LitElement) {
  static get is() {
    return 'bbva-expandable-step';
  }

  static get properties() {
    return {
      /**
       * Current step index number (zero indexed).
       */
      step: {
        type: Number,
      },

      /**
       * Total of steps.
       */
      totalSteps: {
        type: Number,
        attribute: 'total-steps',
      },

      /**
       * Returns the completed state.
       * @default false
       */
      completed: {
        type: Boolean,
        reflect: true,
      },

      /**
       * Event to listen to that will set `completed` as true before edition.
       * The event comes from an element in its light DOM.
       * @default 'complete'
       */
      completeOnEvent: {
        type: String,
        attribute: 'complete-on-event',
      },

      /**
       * Sets the step as not editable.
       * @default false
       */
      disabled: {
        type: Boolean,
        reflect: true,
      },

      /**
       * Selector of the element in light DOM, like a button to continue to next
       * step, that should be disabled while the step is disabled.
       */
      disabledTarget: {
        type: String,
        attribute: 'disabled-target',
      },

      /**
       * Attribute to set on the disabled target element when `disabled`
       * changes.
       * @default 'disabled'
       */
      disabledTargetAttr: {
        type: String,
        attribute: 'disabled-target-attr',
      },

      /**
       * Sets / returns the step that is currently being edited.
       * @default false
       */
      active: {
        type: Boolean,
        reflect: true,
      },

      /**
       * Sets the step as completed with prefilled and not editable content.
       * @default false
       */
      prefilled: {
        type: Boolean,
        reflect: true,
      },

      /**
       * Step header.
       */
      header: {
        type: String,
      },

      /**
       * Level (`aria-level`) of the step header.
       * @default 2
       */
      headingLevel: {
        type: Number,
        attribute: 'heading-level',
      },

      /**
       * Text or translation key for the current step (1 of 5).
       * @default 'bbva-expandable-step-heading'
       */
      currentStepLabel: {
        type: String,
        attribute: 'current-step-label',
      },

      /**
       * Text or translation key for the edit button (Edit).
       * @default 'bbva-expandable-step-edit'
       */
      editLabel: {
        type: String,
        attribute: 'edit-label',
      },

      /**
       * Set to true to hide the current step number.
       * @default false
       */
      hideStepNumber: {
        type: Boolean,
        attribute: 'hide-step-number',
      },

      /**
       * Set to true to disable step when edit previous steps.
       * @default false
       */
      disableOnPreviousStepEdit: {
        type: Boolean,
        attribute: 'disable-on-previous-step-edit',
      },
    };
  }

  constructor() {
    super();

    this.completed = false;
    this.completeOnEvent = 'complete';
    this.disabled = false;
    this.active = false;
    this.prefilled = false;
    this.headingLevel = 2;
    this.currentStepLabel = 'bbva-expandable-step-heading';
    this.editLabel = 'bbva-expandable-step-edit';
    this.hideStepNumber = false;
    this.disabledTargetAttr = 'disabled';
  }

  firstUpdated() {
    this.addEventListener(this.completeOnEvent, this._onCompleteEventFired);
  }

  static get shadyStyles() {
    return `
      ${styles.cssText}
      ${getComponentSharedStyles('bbva-expandable-step-shared-styles').cssText}
    `;
  }

  render() {
    return html`
      <style>
        ${this.constructor.shadyStyles}
      </style>

      <div class="header">
        <cells-heading .level="${this.headingLevel}" class="heading" id="heading">
          <span class="heading-label" id="headingLabel">${this.header}</span>
          <span class="current-step" id="currentStep" ?hidden="${this._computeHiddenStep}">
            (${this.t(this.currentStepLabel, '', this._stepHeadingInterpolation)})
          </span>
        </cells-heading>

        <button
          id="editButton"
          @click="${this.edit}"
          ?hidden="${this._computeHiddenEditButton}"
          class="btn-edit"
        >
          ${this.t(this.editLabel)}
        </button>
      </div>

      <div class="default-content" ?hidden="${this.completed}">
        <slot></slot>
      </div>

      <div class="completed-content">
        <slot name="summary"></slot>
      </div>
    `;
  }

  updated(changedProps) {
    if (changedProps.has('completed')) {
      this._completedChanged(this.completed);
      this._onDisabledChanged(this.disabled, this.completed);
    }

    if (changedProps.has('disabled')) {
      this._disabledChanged(this.disabled);
      this._onDisabledChanged(this.disabled, this.completed);
    }

    if (changedProps.has('prefilled')) {
      this._prefilledChanged(this.prefilled);
    }
  }

  get _stepHeadingInterpolation() {
    return {
      step: this.step + 1,
      total: this.totalSteps,
    };
  }

  get _computeHiddenStep() {
    if (this.hideStepNumber) {
      return true;
    }

    return this.completed;
  }

  get _computeHiddenEditButton() {
    if (this.prefilled) {
      return true;
    }

    return !this.completed;
  }

  _completedChanged(completed) {
    /**
     * Fired whenever "completed" changes.
     * @event completed-changed
     * @param {Boolean} detail completed state
     */
    this.dispatchEvent(
      new CustomEvent('completed-changed', {
        bubbles: true,
        composed: true,
        detail: completed,
      }),
    );
  }

  _disabledChanged(disabled) {
    const disabledTarget = this.querySelector(this.disabledTarget);
    if (disabledTarget) {
      disabledTarget.toggleAttribute(this.disabledTargetAttr, disabled);
    }

    /**
     * Fired when disabled changes.
     * @event disabled-changed
     * @param {Object} detail
     * @param {Boolean} detail.value disabled value
     */
    this.dispatchEvent(
      new CustomEvent('disabled-changed', {
        detail: {
          value: disabled,
        },
      }),
    );
  }

  _prefilledChanged(prefilled) {
    if (prefilled) {
      this.completed = true;
    }
  }

  _onDisabledChanged(disabled, completed) {
    if (disabled && completed) {
      this.disabled = false;
    }
  }

  _onCompleteEventFired(event) {
    event.stopPropagation();
    this.completed = true;
  }

  /**
   * Edits a step. Sets it as not completed and active.
   */
  edit() {
    if (this.prefilled) {
      return;
    }

    this.completed = false;
    this.active = true;

    /**
     * Fired after clicking the "Edit" button in the step header
     * @event edit-step
     * @param {Number} detail Step index
     */
    this.dispatchEvent(
      new CustomEvent('edit-step', {
        bubbles: true,
        composed: true,
        detail: this.step,
      }),
    );
  }
}

customElements.define(BbvaExpandableStep.is, BbvaExpandableStep);
