import { LitElement, html } from 'lit-element';
import { getComponentSharedStyles } from '@cells-components/cells-lit-helpers';
import { CellsI18nMixin as cellsI18nMixin } from '@cells-components/cells-i18n-mixin';
import 'scroll-behavior-polyfill';
import styles from './bbva-expandable-multistep-styles.js';

/**
`<bbva-expandable-multistep>` handles a group of steps (`<bbva-expandable-step>` by default) that can be active, disabled (not edited yet) or completed (already edited and completed).

When a step is being edited it's scrolled to the top of the container component (`<bbva-expandable-multistep>`).

Each step has a `slot` named `summary` that will show the completed content (result after edition). The default content is placed in the default `slot`.

Example:
```html
<bbva-expandable-multistep>
  <bbva-expandable-step header="Step 1">
    <div class="content">
      <button>Continue</button>
    </div>

    <div class="summary" slot="summary">
      <p>Completed content</p>
    </div>
  </bbva-expandable-step>
</bbva-expandable-multistep>
```

## Styling

The following custom properties and mixins are available for styling:

### @apply
| Mixins                                             | Selector                                       | Value |
| -------------------------------------------------- | ---------------------------------------------- | ----- |
| --bbva-expandable-multistep                        | Mixin applied to `<bbva-expandable-multistep>` | {}    |

@customElement bbva-expandable-multistep
@polymer
@LitElement
@demo demo/index.html
@appliesMixin CellsI18nMixin
*/

class BbvaExpandableMultistep extends cellsI18nMixin(LitElement) {
  static get is() {
    return 'bbva-expandable-multistep';
  }

  static get properties() {
    return {
      /**
       * CSS selector used to query step elements in light DOM.
       * The step element should have the same API that `<bbva-expandable-step>` to work properly.
       * @default 'bbva-expandable-step'
       */
      stepSelector: {
        type: String,
        attribute: 'step-selector',
      },

      /**
       * Delay in milliseconds of the scroll animation.
       * @default 900
       */
      scrollingDelay: {
        type: Number,
        attribute: 'scrolling-delay',
      },

      /**
       * Set to true to hide the step numbers in each step.
       * @default false
       */
      hideStepNumbers: {
        type: Boolean,
        attribute: 'hide-step-numbers',
      },
    };
  }

  constructor() {
    super();

    this._allCompleted = false;
    this.stepSelector = 'bbva-expandable-step';
    this.scrollingDelay = 900;
    this.hideStepNumbers = false;

    // Stores the indexes of steps that have been previously completed.
    this._completedSteps = [];
  }

  static get shadyStyles() {
    return `
      ${styles.cssText}
      ${getComponentSharedStyles('bbva-expandable-multistep-shared-styles').cssText}
    `;
  }

  render() {
    return html`
      <style>
        ${this.constructor.shadyStyles}
      </style>
      <slot id="content"></slot>
    `;
  }

  firstUpdated() {
    setTimeout(() => {
      this._setupSteps();
      this._bindListeners();
    }, 20);
  }

  _bindListeners() {
    this.addEventListener('completed-changed', this._onStepCompletedChanged);
    this.addEventListener('edit-step', this._onEditStep);
  }

  /**
   * Returns the completed state (all steps completed).
   * @type {Boolean}
   * @default false
   */
  get allCompleted() {
    return this._allCompleted;
  }

  get _firstStep() {
    return this._steps[0];
  }

  get _lastStep() {
    return this._steps[this._totalSteps - 1];
  }

  get _firstIncompletedStep() {
    return this._steps.filter(step => !step.completed)[0];
  }

  _setupSteps() {
    this._steps = [...this.querySelectorAll(this.stepSelector)];
    this._totalSteps = this._steps.length;
    let currentlyPrefilled = 0;

    this._steps.forEach((step, index) => {
      // eslint-disable-next-line no-param-reassign
      step.step = index - currentlyPrefilled;
      currentlyPrefilled += step.hasAttribute('prefilled') ? 1 : 0;
      // eslint-disable-next-line no-param-reassign
      step.totalSteps = this._totalSteps - this._counterPrefilledSteps();
      // eslint-disable-next-line no-param-reassign
      step.disabled = true;
      // eslint-disable-next-line no-param-reassign
      step.hideStepNumber = this.hideStepNumbers;
      this._enableFirstIncompletedStep(step, index);
    });

    this._setLastStepHeight();
  }

  _counterPrefilledSteps() {
    return this._steps.filter(i => i.hasAttribute('prefilled')).length;
  }

  _enableFirstIncompletedStep(step, index) {
    const previousIndex = index - 1;
    const previousStep = this._steps[previousIndex];
    const enableStep = () => {
      // eslint-disable-next-line no-param-reassign
      step.active = true;
      // eslint-disable-next-line no-param-reassign
      step.disabled = false;
    };

    if (!previousStep) {
      enableStep();
    } else if (previousStep.completed) {
      enableStep();
      this._completedSteps.push(previousIndex);
      step.classList.toggle('after-prefilled', previousStep.prefilled);
    }
  }

  _setLastStepHeight() {
    if (!this._offsetHeight) {
      this._offsetHeight = document.body.offsetHeight;
    }

    this._lastStep.style.height = `${this._offsetHeight}px`;
  }

  _removeLastStepHeight() {
    this._lastStep.style.height = '';
  }

  _onStepCompletedChanged(event) {
    const stepCompleted = event.detail;
    const stepIndex = event.target.step;

    this._computeAllCompleted();

    if (stepCompleted) {
      const step = this._steps[stepIndex];
      step.active = false;

      this._scrollToStep(step, 0);

      if (!this._completedSteps.includes(stepIndex)) {
        this._completedSteps.push(stepIndex);
      }
    }
  }

  _scrollToStep(step, delay = this.scrollingDelay) {
    this.classList.remove('scrollable');

    setTimeout(() => {
      step.scrollIntoView({ block: 'start', behavior: 'smooth' });
      this._onScrollAnimationCompleted(step);
    }, delay);
  }

  _onScrollAnimationCompleted(step) {
    if (!step.active) {
      const nextStep = this._firstIncompletedStep || this._firstStep;
      nextStep.active = true;
      this._scrollToStep(nextStep);
      return;
    }

    // eslint-disable-next-line no-param-reassign
    step.disabled = false;
    this.classList.add('scrollable');

    if (this.allCompleted) {
      setTimeout(() => {
        this._removeLastStepHeight();
      }, this.scrollingDelay);
    }

    /**
     * Fired when a scroll animation ends
     * @event scroll-animation-end
     */
    this.dispatchEvent(
      new CustomEvent('scroll-animation-end', {
        bubbles: true,
        composed: true,
      }),
    );
  }

  _onEditStep(event) {
    const editedStepIndex = event.detail + this._counterPrefilledSteps();
    const editedStep = this._steps[editedStepIndex];
    editedStep.active = true;

    this._scrollToStep(editedStep);

    this._steps.forEach((step, index) => {
      this._deactivateOtherSteps(step, index, editedStepIndex);
      this._disableNextIncompletedSteps(step, index, editedStepIndex);
      this._completeActiveStepsPreviouslyCompleted(step, index, editedStepIndex);
      this._forceDisabledStep(step, index);
    });
  }

  _forceDisabledStep(step, index) {
    if (step.disableOnPreviousStepEdit) {
      // eslint-disable-next-line no-param-reassign
      step.completed = false;
      // eslint-disable-next-line no-param-reassign
      step.disabled = true;
      this._completedSteps = this._completedSteps.filter(item => item !== index);
      this._computeAllCompleted();
    }
  }

  // eslint-disable-next-line class-methods-use-this
  _deactivateOtherSteps(step, index, editedStepIndex) {
    if (index !== editedStepIndex) {
      // eslint-disable-next-line no-param-reassign
      step.active = false;
    }
  }

  // eslint-disable-next-line class-methods-use-this
  _disableNextIncompletedSteps(step, index, editedStepIndex) {
    if (!step.completed && index > editedStepIndex) {
      // eslint-disable-next-line no-param-reassign
      step.disabled = true;
    }
  }

  /**
   * Discard changes in a step being edited (previously completed) when the
   * user clicks "Edit" in other step without completing the current one.
   */
  _completeActiveStepsPreviouslyCompleted(step, index, editedStepIndex) {
    if (index !== editedStepIndex && this._completedSteps.includes(index)) {
      // eslint-disable-next-line no-param-reassign
      step.completed = true;
    }
  }

  _allCompletedChanged(allCompleted, previouslyCompleted) {
    if (previouslyCompleted) {
      this._setLastStepHeight();
    }

    /**
     * Fired when allCompleted changes
     * @event all-completed
     * @param {Boolean} detail Completed status
     */
    this.dispatchEvent(
      new CustomEvent('all-completed', {
        detail: allCompleted,
        bubbles: true,
        composed: true,
      }),
    );
  }

  _computeAllCompleted() {
    const previousCompletedValue = this._allCompleted;

    this._allCompleted = this._steps.every(step => step.completed);
    this.toggleAttribute('all-completed', this._allCompleted);
    this._allCompletedChanged(this._allCompleted, previousCompletedValue);
  }
}

customElements.define(BbvaExpandableMultistep.is, BbvaExpandableMultistep);
