import '@webcomponents/shadycss/entrypoints/scoping-shim.js';
import '@webcomponents/shadycss/entrypoints/apply-shim.js';
import './elements/cells-dom-module.js';
import './elements/cells-custom-style.js';

export const setComponentSharedStyles = (id, styles) => {
  const domModule = document.createElement('cells-dom-module');
  domModule[id] = styles;
  domModule.register(id);
};

export const setDocumentCustomStyles = (styles) => {
  const customStyles = document.createElement('template');
  customStyles.setAttribute('style', 'display: none;');
  customStyles.innerHTML = `
  <cells-custom-style>
    <style>
      ${styles.cssText}
    </style>
  </cells-custom-style>
  `;
  document.head.appendChild(customStyles.content);
};
