import { css } from 'lit-element';

export default css`:host {
  display: block;
  box-sizing: border-box;
  height: 100%;
  overflow: hidden;
  @apply --bbva-expandable-multistep; }

:host([hidden]), [hidden] {
  display: none !important; }

*, *:before, *:after {
  box-sizing: inherit; }

:host(.scrollable) {
  overflow-y: auto;
  touch-action: pan-y;
  -webkit-overflow-scrolling: touch; }
`;