import { LitElement, html, } from 'lit-element';
import { getComponentSharedStyles, } from '@cells-components/cells-lit-helpers/cells-lit-helpers.js';
/**
`<cells-heading>` provides heading semantics and an easy way to customize and unify heading styles used across a website or application.

__Example:__

```html
<cells-heading class="custom-class">Heading</cells-heading>
```

__Heading levels:__

You can provide information about the heading level relative to the document by setting the `level` attribute with a numeric value from 1 to 6. By default the heading level (`aria-level`) is set to 2.
If 0 is provided, the component won't be treated as a heading.

```html
<cells-heading level="1">Application heading</cells-heading>
```

## Styling

`<cells-heading>` has no styles, custom CSS properties or available mixins. You can customize it
by creating a style module with ID `cells-heading-shared-styles` where you can define the styles
that will be applied to the tag using your own custom classes.

* @demo demo/index.html
* @extends {LitElement}
* @customElement
*/
class cellsHeading extends LitElement {
  static get is() {
    return 'cells-heading';
  }

  static get properties() {
    return {
      /**
       * Allows to define an aria-level for the main header text.
       * If 0 is provided, the header text won't be treated as a heading.
       */
      level: {
        type: Number
      }
    };
  }

  constructor() {
    super();
    this.level = 2;
  }

  updated(changedProps) {
    if (changedProps.has('level')) {
      this._levelChanged(this.level);
      this.dispatchEvent(new CustomEvent('level-changed', {
        detail: {
          value: this.level
        }
      }));
    }
  }

  static get shadyStyles() {
    return `
      :host { display: block; }
      :host([hidden]) { display: none; }
      ${getComponentSharedStyles('cells-heading-shared-styles')}
    `;
  }

  render() {
    return html`
      <style>${this.constructor.shadyStyles}</style>
      <slot></slot>
    `;
  }

  _levelChanged(level) {
    if (level >= 1 && level <= 6) {
      this.setAttribute('aria-level', level);
      this.setAttribute('role', 'heading');
    } else {
      this.removeAttribute('aria-level');
      this.removeAttribute('role');
    }
  }
}

customElements.define(cellsHeading.is, cellsHeading);
export default cellsHeading;
