import { html } from 'lit-element';
import { getComponentSharedStyles } from '@cells-components/cells-lit-helpers';
import { CellsAmountMixin as cellsAmountMixin } from '@cells-components/cells-amount-mixin';
import * as AmountUtils from '@cells-components/cells-amount-mixin/utils/amount-utilities.js';
import { BbvaFormField } from '@bbva-web-components/bbva-form-field';
import styles from './bbva-form-amount-styles.js';

export { bbvaFormFieldAmbient } from '@bbva-web-components/bbva-form-field';
/**
Amount fields are used to allow a user to type in an amount. This is commonly used for transfers and for paying bills.

This component extends `BbvaFormField` for field styling & functionality, and applies `CellsAmountMixin` for common currency properties: `amount`, `currencyCode`, `localCurrency` and `language`. It provides automatic currency formatting as user writes based on those properties, and fires an event on input changes containing both formatted & unformatted amount.


**Examples:**
```html
<bbva-form-amount label="Enter amount"></bbva-form-amount>
<bbva-form-amount label="With default" amount="12345.67"></bbva-form-amount>
<bbva-form-amount label="Currency" currency-code="USD" local-currency="USD" language="en"></bbva-form-amount>
<bbva-form-amount label="No decimals" amount="12345.67" decimal-digits="0"></bbva-form-amount>
<bbva-form-amount label="Disabled" disabled amount="12345.67"></bbva-form-amount>
<bbva-form-amount label="Readonly" readonly amount="12345.67"></bbva-form-amount>
<bbva-form-amount label="Required" required></bbva-form-amount>
```

## Styling

The following mixins are available for styling:

### @apply
| Selector                                                    | Mixin                                        |
|:------------------------------------------------------------|:---------------------------------------------|
| :host                                                       | --bbva-form-amount                     |
| ::slotted(input)                                            | --bbva-form-amount-input               |
| :host([focused]) .back, :host([filled]) .back               | --bbva-form-amount-focused-back        |
| :host([focused]) .back-amount, :host([filled]) .back-amount | --bbva-form-amount-focused-back-amount |
| .back-amount                                                | --bbva-form-amount-back-amount         |
| .back-currency                                              | --bbva-form-amount-back-currency       |
| .back                                                       | --bbva-form-amount-back                |

@customElement bbva-form-amount
@polymer
@LitElement
@demo demo/index.html
@appliesMixin CellsAmountMixin
*/
export class BbvaFormAmount extends cellsAmountMixin(BbvaFormField) {
  static get is() {
    return 'bbva-form-amount';
  }

  static get properties() {
    return {
      /**
       * Number of decimals
       */
      decimalDigits: {
        type: Number,
        attribute: 'decimal-digits',
      },
    };
  }

  constructor() {
    super();
    this.decimalDigits = 2;
  }

  connectedCallback() {
    /* istanbul ignore if */
    if (super.connectedCallback) {
      super.connectedCallback();
    }

    this._checkTrailingDecimal = this._checkTrailingDecimal.bind(this);
    this.addEventListener('blur', this._checkTrailingDecimal);
  }

  firstUpdated(changedProps) {
    /* istanbul ignore if */
    if (super.firstUpdated) {
      super.firstUpdated(changedProps);
    }
    this._currencyBlock = this.shadowRoot.querySelector('.back-currency');
  }

  updated(changedProps) {
    /* istanbul ignore if */
    if (super.updated) {
      super.updated(changedProps);
    }
    if (changedProps.has('language')) {
      this.allowedPattern = `[0-9${this._separator}${this._groupChar}]`;
    }
    if (changedProps.has('value')) {
      this.formatValue(this.value);
    }
    if (changedProps.has('localCurrency') || changedProps.has('currencyCode')) {
      this._setCurrencyWidth();
    }
  }

  static get shadyStyles() {
    return `
      ${super.shadyStyles}
      ${styles.cssText}
      ${getComponentSharedStyles('bbva-form-amount-shared-styles').cssText}
    `;
  }

  /**
   * Returns field value formatted as number
   * @return {Number} Value formatted as number
   */
  get amount() {
    return this.value ? Number(
      this.value
        .replace(new RegExp(`\\${this._groupChar}`, 'g'), '')
        .replace(new RegExp(`\\${this._separator}`), '.'),
    ) : 0;
  }

  /**
   * Updates input value with received amount
   * @param  {Number} newValue Amount to use as value
   */
  set amount(newValue) {
    if (Number.isFinite(newValue)) {
      this.value = String(newValue).replace(/\./g, this._separator);
    }
  }

  get _separator() {
    return AmountUtils.getSeparator(this.language);
  }

  get _groupChar() {
    return AmountUtils.getGroupChars(this.language);
  }

  get _currency() {
    return AmountUtils.getCurrencyAsSymbol(this.localCurrency, this.currencyCode);
  }

  get _isRightAligned() {
    return AmountUtils.isRightAligned(this.currencyCode);
  }

  /**
   * HTML for label block, directly using label property as visual label
   * @return {TemplateResult} HTML for visual label block
   */
  get labelBlock() {
    return html`
      <span class="label" aria-hidden="true" @click="${this.focus}">${this.label}</span>
    `;
  }

  /**
   * Accessible label to be used for input aria-label, joining label property with currency
   * @return {String} String joining label property and currency
   */
  get inputLabel() {
    return `${this.label} (${this._currency})`;
  }

  /**
   * Layer containing label, input and field buttons with listeners for focusin and focusout, as well as back amount block for currency symbol
   * @return {TemplateResult} HTML of field containing input, label, clear button and back amount block
   */
  get field() {
    return html`
      <div class="field" @focusin="${this._onFocusin}" @focusout="${this._onFocusout}">
        ${this.backAmount} ${this.labelledInput} ${this.clearButton}
      </div>
    `;
  }

  /**
   * Span with transparent formatted amount and visible currency symbol
   * @return {TemplateResult} HTML of transparent amount and currency symbol
   */
  get backAmount() {
    return html`
      <span class="back" aria-hidden="true">
        <span class="back-currency ${this._isRightAligned ? 'right' : ''}">${this._currency}</span>
        <span class="back-amount">${this.value}</span>
      </span>
    `;
  }

  _setCurrencyWidth() {
    const width = this._isRightAligned ? 0 : this._currencyBlock.offsetWidth;
    //this.style.setProperty('--_currency-width', `${width / 16}rem`);
    this.style.setProperty('--_currency-width', `0.7 rem`);
  }

  _formatNumber(n, group) {
    // format number 1000000 to 1,234,567
    if (group) {
      return n.replace(/\D/g, '').replace(/\B(?=(\d{3})+(?!\d))/g, this._groupChar);
    }
    return n.replace(/\D/g, '');
  }

  _formatCurrency(value, decimal) {
    let result;
    // check for decimal
    if (value.indexOf(decimal) >= 0) {
      // get position of first decimal
      // this prevents multiple decimals from
      // being entered
      const decimalPos = value.indexOf(decimal);

      // split number by decimal point
      let leftSide = value.substring(0, decimalPos);
      let rightSide = value.substring(decimalPos);

      // add commas to left side of number
      leftSide = this._formatNumber(leftSide, true);

      // validate right side
      rightSide = this._formatNumber(rightSide, false);
      // Limit decimal to only decimalDigits
      rightSide = rightSide.substring(0, this.decimalDigits);

      // join number by .
      result = this.decimalDigits ? leftSide + decimal + rightSide : leftSide;
    } else {
      // no decimal entered
      // add commas to number
      // remove all non-digits
      result = this._formatNumber(value, true);
    }

    return result;
  }

  /**
   * Formats received value based on element currencyCode, localCurrency and language
   * @param  {String} value Input value
   */
  formatValue(value) {
    // don't validate empty input
    if (value === '' || value === undefined || value === null) {
      this._fireChange(value, this.amount);
      return;
    }

    const stringValue = String(value);
    const input = this._formElement;

    // original length
    const originalLen = stringValue.length;
    // initial caret position
    let caretPos = input.selectionStart;

    const formattedValue = this._formatCurrency(stringValue, this._separator);

    // send updated string to input
    input.value = formattedValue;
    if (stringValue === formattedValue) {
      this._fireChange(formattedValue, this.amount);
    }
    this.value = input.value;

    // put caret back in the right position
    const updatedLen = formattedValue.length;
    caretPos = updatedLen - originalLen + caretPos;
    input.setSelectionRange(caretPos, caretPos);
  }

  _checkTrailingDecimal() {
    if (this.value && this.value.endsWith(this._separator)) {
      this.value = this.value.replace(this._separator, '');
    }
  }

  _fireChange(value, amount) {
    /**
     * Fired when input value changes and is formatted
     * @event bbva-form-amount-change
     * @param {String} value Formatted amount
     * @param {Number} amount Amount as number
     */
    this.dispatchEvent(
      new CustomEvent('bbva-form-amount-change', {
        bubbles: true,
        composed: true,
        detail: {
          value,
          amount,
        },
      }),
    );
  }
}

customElements.define(BbvaFormAmount.is, BbvaFormAmount);
