import { LitElement, html } from 'lit-element';
import { getComponentSharedStyles } from '@cells-components/cells-lit-helpers';
import { CellsI18nMixin as cellsI18nMixin } from '@cells-components/cells-i18n-mixin';
import { ifDefined } from 'lit-html/directives/if-defined.js';
import '@cells-components/cells-icon';
import styles from './bbva-clip-box-styles.js';

const randomColors = [
  'bbva-medium-blue',
  'bbva-core-blue',
  'bbva-dark-aqua',
  'bbva-dark-red',
  'bbva-dark-orange',
  'bbva-dark-yellow',
  'bbva-dark-gold',
  'bbva-dark-coral',
  'bbva-dark-green',
  'bbva-dark-pink',
  'bbva-dark-purple',
  'bbva-500',
];
const maxSizeInitials = 2;

const hashCode = s => s.split('').reduce((a, b) => a + b.charCodeAt(0), 0);

/**
A circular resource component used for initial clips, photos, or icons.

Example:
```html
<bbva-clip-box
  class="medium bitone"
  initials="aaaa"></bbva-clip-box>

<bbva-clip-box
  bg-color="bbva-medium-aqua"
  class="medium bitone"
  icon="coronita:myprofile"></bbva-clip-box>

  <bbva-clip-box
  class="medium"
  image="https://pbs.twimg.com/profile_images/833767319973212161/Ft904pMk_400x400.jpg"></bbva-clip-box>
```

## Random Colors

n the case of the initials, the color of the background should be assigned to the wording of the colors that pass accessibility, and if it is a contact in the agenda, the same color should always be kept for that person.
These are the 12 colors available:
* bbva-medium-blue
* bbva-dark-yellow
* bbva-dark-purple
* bbva-core-blue
* bbva-dark-gold
* bbva-500
* bbva-dark-aqua
* bbva-dark-coral
* bbva-dark-red
* bbva-dark-green
* bbva-dark-orange
* bbva-dark-pink

## Icons

Since this component uses icons, it will need an [iconset](https://platform.bbva.com/en-us/developers/engines/cells/documentation/cells-architecture/composing-with-components/cells-icons) in your project as an application level dependency. In fact, this component uses an iconset in its demo.

## Styling

The following custom properties and mixins are available for styling:

| Custom Property           | Description                                | Default                  |
| :------------------------ | :----------------------------------------- | :----------------------- |
| --bbva-clip-box-bg-color  | Background color                           | bbva-medium-blue         |
| --bbva-clip-box-size      | Size. Can be small, medium and large       | 40 x 40                  |
| --bbva-clip-box-bitone-bg | Background color applied to :host(.bitone) | var(--bbva-600, #121212) |
| --bbva-clip-box-font-size | Text font-size                             | 0.4em                    |

@customElement bbva-clip-box
@polymer
@LitElement
@demo demo/index.html
@appliesMixin CellsI18nMixin
*/
class BbvaClipBox extends cellsI18nMixin(LitElement) {
  static get is() {
    return 'bbva-clip-box';
  }

  static get properties() {
    return {
      /**
       * Background color
       */
      bgColor: {
        type: String,
        attribute: 'bg-color',
      },
      /**
       * Accessibility Text
       */
      accessibilityText: {
        type: String,
        attribute: 'accessibility-text',
      },
      /**
       * Icon showed
       */
      icon: {
        type: String,
      },
      /**
       * Text
       */
      text: {
        type: String,
      },
      /**
       * Image source
       */
      image: {
        type: String,
      },
    };
  }

  constructor() {
    super();
    this.bgColor = '';
    this.accessibilityText = '';
    this.icon = '';
    this.text = '';
    this.image = '';
  }

  static get shadyStyles() {
    return `
      ${styles.cssText}
      ${getComponentSharedStyles('bbva-clip-box-shared-styles').cssText}
    `;
  }

  render() {
    return html`
      <style>
        ${this.constructor.shadyStyles}
      </style>
      <div class="bitone-layer"></div>
      <p class="initials" id="initials" aria-label="${this.t(this.text)}" ?hidden="${!this.text}">
        ${this._initialsTransform}
      </p>
      <cells-icon
        ?aria-hidden="${this._computedIsAriaHidden}"
        aria-label="${this.t(this.accessibilityText)}"
        class="${this._addClassComputed}"
        id="icon"
        .icon="${this.icon}"
        .src="${this.image}"
        role="${ifDefined(this._computedRole)}"
        ?hidden="${this._hasIconImage}"
      >
      </cells-icon>
    `;
  }

  updated(changedProps) {
    if (changedProps.has('bgColor') || changedProps.has('text')) {
      this._updateBgColor(this.bgColor, this._initialsTransform);
    }
  }

  _updateBgColor(bgColor, initialsFormat) {
    if (initialsFormat) {
      const index = hashCode(initialsFormat) % randomColors.length;
      this._setBgColor(randomColors[index]);
    } else if (bgColor !== '') {
      this._setBgColor(this.bgColor);
    }
  }

  get _hasIconImage() {
    return !(this.icon || this.image);
  }

  get _computedIsAriaHidden() {
    return String(Boolean(this.image && this.accessibilityText));
  }

  get _addClassComputed() {
    if (this.icon) {
      return 'icon';
    }
    if (this.image) {
      return 'image';
    }
    return '';
  }

  get _initialsTransform() {
    return this.text.substr(0, maxSizeInitials).toUpperCase();
  }

  get _computedRole() {
    return this.image ? 'image' : undefined;
  }

  _setBgColor(color) {
    this.style.setProperty('--bbva-clip-box-bg-color', `var(--${color})`);
  }
}

customElements.define(BbvaClipBox.is, BbvaClipBox);
