import { LitElement, html } from 'lit-element';
import { getComponentSharedStyles } from '@cells-components/cells-lit-helpers/cells-lit-helpers.js';
import styles from './bbva-clip-flag-styles.js';
import '@cells-components/cells-icon/cells-icon.js';

/**
A flag clip is used to represents each countries currencies.
This component renders country flags given their country id (Currency ISO code)

## Styling


### @apply
|  Selector  |            Mixin            |
| :--------- | :-------------------------- |
| :host      | --bbva-card-control         |
| cells-icon | --bbva-clip-flag-cells-icon |

 Example:

 ```html
 <bbva-clip-flag
 icon="uy"
 size="small">
 </bbva-clip-flag>
 ```

@customElement bbva-clip-flag
@polymer
@LitElement
@demo demo/index.html
*/
export class BbvaClipFlag extends LitElement {
  static get is() {
    return 'bbva-clip-flag';
  }

  static get properties() {
    return {
      /**
       * Flag image size
       * Values: "small", "medium" or "large"
       */
      size: { type: String },
      /**
       * The name of the icon to use. The name should be of the form:
       * `iconset_name:icon_name`.
       */
      icon: { type: String },
      /**
       * Iconset name
       * @type { String }
       */
      iconset: { type: String },

      /**
       * Width for icons
       */
      width: { type: Number },

      /**
       * Height for icons
       */
      height: { type: Number },
    };
  }

  // Initialize properties
  constructor() {
    super();
    this.iconset = 'flags';
  }

  /**
   * Return default flag sizes
   */
  static get _sizes() {
    return {
      small: {
        width: 48,
        height: 32,
      },
      medium: {
        width: 60,
        height: 40,
      },
      large: {
        width: 72,
        height: 48,
      },
    };
  }

  /**
   * Sets flag icon size
   * @param {String} size
   */
  set _sizes(size) {
    const sizes = this.constructor._sizes;
    // eslint-disable-next-line no-prototype-builtins
    const targetSize = sizes.hasOwnProperty(size) ? size : 'small';
    this.width = sizes[targetSize].width;
    this.height = sizes[targetSize].height;
  }

  updated(changedProps) {
    if (changedProps.has('size')) {
      this._sizes = this.size;
    }
  }

  static get shadyStyles() {
    return `
      ${styles.cssText}
      ${getComponentSharedStyles('bbva-clip-flag-shared-styles').cssText}
    `;
  }

  render() {
    return html`
      <style>
        ${this.constructor.shadyStyles}
      </style>
      <cells-icon
        icon="${this.iconset}:${this.icon}"
        width="${this.width}"
        height="${this.height}"
      ></cells-icon>
    `;
  }
}

// Register the element with the browser
customElements.define(BbvaClipFlag.is, BbvaClipFlag);
