import { LitElement, html } from 'lit-element';
import { ifDefined } from 'lit-html/directives/if-defined.js';
import { getComponentSharedStyles } from '@cells-components/cells-lit-helpers';
import '@cells-components/cells-icon';
import '@bbva-web-components/bbva-clip-box';
import '@cells-components/cells-collapse';
import '@bbva-web-components/bbva-amount/bbva-amount.js';
import { CellsAmountMixin as cellsAmountMixin } from '@cells-components/cells-amount-mixin';
import '@bbva-web-components/bbva-badge-default/bbva-badge-default.js';
import '@bbva-web-components/bbva-badge-notification/bbva-badge-notification.js';
import styles from './bbva-expandable-accordion-styles.js';

/**
Renders an expandable accordion.

Example:

```html
__ Expandable Accordion default
<bbva-expandable-accordion
  row-title="Account"
  amount="9999.99"
  currency-code="USD"
  local-currency="USD"
  language="es"
  amount-badge="Available"
  amount-badge-class="success"
  notification-badge-text="3">
</bbva-expandable-accordion>

__ Expandable Accordion icon
<bbva-expandable-accordion
  icon="coronita:bar"
  row-title="Tus ahorros">
</bbva-expandable-accordion>

__ Expandable Accordion button
<bbva-expandable-accordion
  clip-box
  icon="coronita:tools"
  row-title="Tus ahorros">
</bbva-expandable-accordion>

__ Expandable Accordion description
<bbva-expandable-accordion row-title="Tus ahorros">
</bbva-expandable-accordion>
```

## Icons

Since this component uses icons, it will need an [iconset](https://platform.bbva.com/en-us/developers/engines/cells/documentation/cells-architecture/components/components-in-depth/icons) in your project as an application level dependency. In fact, this component uses an iconset in its demo.

## Styling

The following custom properties and mixins are available for styling:

| Custom Property                                             | Description      | Value                                |
|:------------------------------------------------------------|:-----------------|:-------------------------------------|
| --bbva-expandable-accordion-divider-color                   | Border color     | var(--bbva-300, #D3D3D3)             |
| --bbva-expandable-accordion-left-icon-color                 | Left Icon color  | var(--bbva-400, #bdbdbd)             |
| --bbva-expandable-accordion-right-icon-color                | Right Icon color | var(--bbva-white-core-blue, #1464A5) |
| --bbva-expandable-accordion-text-color                      | Text color       | var(--bbva-600, #121212)             |

| Mixins                                         | Selector                      | Value |
|:-----------------------------------------------|:------------------------------|:------|
| --bbva-expandable-accordion                    | Mixin for component           | {}    |
| --bbva-expandable-accordion-header             | Mixin for .header-accordion   | {}    |
| --bbva-expandable-accordion-left-icon          | Mixin for .left-icon          | {}    |
| --bbva-expandable-accordion-text               | Mixin for text                | {}    |
| --bbva-expandable-accordion-clip-box           | Mixin for bbva-clip-box       | {}    |
| --bbva-expandable-accordion-content            | Mixin for .collapse-content   | {}    |
| --bbva-expandable-accordion-amount             | Mixin for bbva-amount         | {}    |

@customElement bbva-expandable-accordion
@polymer
@LitElement
@demo demo/index.html
@appliesMixin CellsAmountMixin
*/
class BbvaExpandableAccordion extends cellsAmountMixin(LitElement) {
  static get is() {
    return 'bbva-expandable-accordion';
  }

  static get properties() {
    return {
      /**
       * Flag representing if it has clip box
       */
      clipBox: {
        type: Boolean,
        attribute: 'clip-box',
      },
      /**
       * Icon for the left side
       */
      icon: {
        type: String,
      },
      /**
       * Icon left size
       */
      iconSize: {
        type: Number,
        attribute: 'icon-size',
      },
      /**
       * Background color for open clip box
       */
      bgClipBoxOpen: {
        type: String,
        attribute: 'bg-clip-box-open',
      },
      /**
       * Background color for closed clip box
       */
      bgClipBoxClose: {
        type: String,
        attribute: 'bbva-medium-blue',
      },
      /**
       * Open state Icon
       */
      openIcon: {
        type: String,
        attribute: 'open-icon',
      },
      /**
       * Close state Icon
       */
      closeIcon: {
        type: String,
        attribute: 'close-icon',
      },
      /**
       * Open or close Icon size
       */
      stateIconSize: {
        type: Number,
        attribute: 'state-icon-size',
      },
      /**
       * Accordion title
       */
      rowTitle: {
        type: String,
        attribute: 'row-title',
      },
      /**
       * Notification count to show
       */
      notificationBadgeText: {
        type: Number,
        attribute: 'notification-badge-text',
      },
      /**
       * Accessible text for notification count
       */
      notificationBadgeAccessibilityText: {
        type: String,
        attribute: 'notification-badge-accessibility-text',
      },
      /**
       * Text badge for amount
       */
      amountBadge: {
        type: String,
        attribute: 'amount-badge',
      },
      /**
       * Class for amount text badge. Check 'bbva-badge-default' for available color classes.
       */
      amountBadgeClass: {
        type: String,
        attribute: 'amount-badge-class',
      },
      /**
       * Id for aria-controls
       */
      _uniqueId: {
        type: Number,
      },
      /**
       * Flag to represent if accordion is open
       */
      opened: {
        type: Boolean,
      },
    };
  }

  constructor() {
    super();
    this.clipBox = false;
    this.iconSize = 24;
    this.bgClipBoxOpen = 'bbva-500';
    this.bgClipBoxClose = 'bbva-medium-blue';
    this.openIcon = 'coronita:expand';
    this.closeIcon = 'coronita:collapse';
    this.stateIconSize = 24;
    this.bgClipBox = '';
    this.amountBadgeClass = 'error';
    this.opened = false;
    this._uniqueId = Math.floor(Math.random() * Date.now());
  }

  updated(changedProps) {
    if (changedProps.has('opened')) {
      this.dispatchEvent(
        new CustomEvent('opened-changed', {
          bubbles: true,
          composed: true,
          detail: {
            value: this.opened,
          },
        }),
      );
    }
  }

  static get shadyStyles() {
    return `
      ${getComponentSharedStyles('bbva-global-dividers-shared-styles').cssText}
      ${styles.cssText}
      ${getComponentSharedStyles('bbva-expandable-accordion-shared-styles').cssText}
    `;
  }

  render() {
    return html`
      <style>
        ${this.constructor.shadyStyles}
      </style>
      <button
        class="header-accordion"
        role="tab"
        aria-selected="${ifDefined(this.opened)}"
        aria-controls="${this._uniqueId}"
        aria-expanded="${ifDefined(this.opened)}"
        @click="${this._onHeaderClick}"
      >
        ${this._hasIcon ? this._icon : ''} ${this._hasClipbox ? this._clipBox : ''}
        <span class="row-title ${this._computedTextCss}"
          >${this.rowTitle}
          <bbva-badge-notification
            .accessibilityText="${ifDefined(this._notificationBadgeAccessibilityText)}"
            .text="${this.notificationBadgeText}"
            ?hidden="${!this.notificationBadgeText}"
          >
          </bbva-badge-notification
        ></span>
        ${this.amount
          ? html`
              <span>
                <bbva-badge-default
                  .text="${ifDefined(this.amountBadge)}"
                  class="${this.amountBadgeClass}"
                >
                </bbva-badge-default>
              </span>
              <bbva-amount
                .amount="${this.amount}"
                .currencyCode="${this.currencyCode}"
                .localCurrency="${this.localCurrency}"
                .language="${this.language}"
              >
              </bbva-amount>
            `
          : ''}
        <cells-icon
          .icon="${this.opened ? this.closeIcon : this.openIcon}"
          .size="${this.stateIconSize}"
          class="right-icon"
        ></cells-icon>
      </button>

      <cells-collapse id="${this._uniqueId}" ?opened="${ifDefined(this.opened)}" data-id="collapse">
        <div class="collapse-content">
          <slot></slot>
        </div>
      </cells-collapse>
    `;
  }

  get _icon() {
    return html`
      <cells-icon class="left-icon" .size="${this.iconSize}" .icon="${this.icon}"> </cells-icon>
    `;
  }

  get _clipBox() {
    return html`
      <bbva-clip-box
        class="small bitone"
        .bgColor="${this.opened ? this.bgClipBoxOpen : this.bgClipBoxClose}"
        .icon="${this.icon}"
      >
      </bbva-clip-box>
    `;
  }

  _onHeaderClick() {
    this.opened = !this.opened;
  }

  get _notificationBadgeAccessibilityText() {
    return this.notificationBadgeAccessibilityText || undefined;
  }

  get _hasIcon() {
    return !this.clipBox && this.icon;
  }

  get _hasClipbox() {
    return this.clipBox && this.icon;
  }

  get _computedTextCss() {
    const css = ['text'];

    if (this.clipBox) {
      css.push('bold-text');
      css.push(this.opened ? 'open' : 'close');
    }
    return css.join(' ');
  }
}

customElements.define(BbvaExpandableAccordion.is, BbvaExpandableAccordion);
