import { html } from 'lit-element';
import { getComponentSharedStyles } from '@cells-components/cells-lit-helpers';
import { BbvaFormField } from '@bbva-web-components/bbva-form-field';
import '@cells-components/cells-icon';
import styles from './bbva-form-date-styles.js';

export { bbvaFormFieldAmbient } from '@bbva-web-components/bbva-form-field';
/**
Date fields are used to allow a user to input a date within a form.

This component extends 'BbvaFormField' to get input field styling & functionality, and exports the same ambients. It does not show a clear button. Instead, it shows a decorative icon.

This component has the same browser compatibility as native date input as well as the same caveats. Check [input date MDN docs](https://developer.mozilla.org/en-US/docs/Web/HTML/Element/input/date) for more info.

**Example:**

```html
<bbva-form-date label="Time label"></bbva-form-date>
<bbva-form-date label="Time label" value="2018-07-22"></bbva-form-date>
<bbva-form-date label="Time label" disabled></bbva-form-date>
<bbva-form-date label="Time label" required></bbva-form-date>
<bbva-form-date label="Time label" autofocus></bbva-form-date>
<bbva-form-date
  label="Field label"
  value="2018-07-22"
  min="2018-06-01"
  max="2018-08-31"
  auto-validate
  pattern="\d{4}-\d{2}-\d{2}"
  form="formId"
></bbva-form-date>
<bbva-form-date
  label="Field label"
  value="2018-07-22"
  min="2018-06-01"
  max="2018-08-31"
  validate-on-blur
  pattern="\d{4}-\d{2}-\d{2}"
></bbva-form-date>
```

## Icons

Since this component uses icons, it will need an [iconset](https://platform.bbva.com/en-us/developers/engines/cells/documentation/cells-architecture/components/components-in-depth/icons) in your project as an application level dependency. In fact, this component uses an iconset in its demo.

## Styling

The following mixins are available for styling, as well as all custom properties and mixins from 'BbvaFormField':

### @apply

| Selector                                                                                                                 | Mixin                          |
| :----------------------------------------------------------------------------------------------------------------------- | :----------------------------- |
| :host                                                                                                                    | --bbva-form-date               |
| ::slotted(input)                                                                                                         | --bbva-form-date-input         |
| :host([focused]) ::slotted(input), :host([invalid][filled]) ::slotted(input)                                             | --bbva-form-date-focused-input |
| :host(:not([filled])) ::slotted(input)                                                                                   | --bbva-form-date-empty-input   |
| :host([focused]) ::slotted(input), :host([readonly][filled]) ::slotted(input), :host([invalid][filled]) ::slotted(input) | --bbva-form-date-visible-input |
| .clear                                                                                                                   | --bbva-form-date-icon          |

@customElement bbva-form-date
@polymer
@LitElement
@demo demo/index.html
*/
export class BbvaFormDate extends BbvaFormField {
  static get is() {
    return 'bbva-form-date';
  }

  constructor() {
    super();
    this.type = 'date';
    this.clearIcon = 'coronita:calendar';
  }

  static get shadyStyles() {
    return `
      ${super.shadyStyles}
      ${styles.cssText}
      ${getComponentSharedStyles('bbva-form-date-shared-styles').cssText}
    `;
  }

  /**
   * Returns native input value as date
   * @return {Object} Date object of current time value
   */
  get valueAsDate() {
    return this._formElement.valueAsDate;
  }

  /**
   * Returns native input value as number
   * @return {Number} Current time value as number
   */
  get valueAsNumber() {
    return this._formElement.valueAsNumber;
  }

  /**
   * Calls native input stepDown method and updates value accordingly
   */
  stepDown() {
    this._formElement.stepDown();
    this.value = this._formElement.value;
  }

  /**
   * Calls native input stepUp method and updates value accordingly
   */
  stepUp() {
    this._formElement.stepUp();
    this.value = this._formElement.value;
  }

  /**
   * HTML of field with clear icon behind labelled input
   * @return {TemplateResult} HTML of field containing input, label and clear icon
   */
  get field() {
    // Add click listener due to firefox not dispatching focusin event on input date mouse click
    return html`
      <div
        class="field"
        @click="${this._onFocusin}"
        @focusin="${this._onFocusin}"
        @focusout="${this._onFocusout}"
      >
        ${this.clearIconBlock} ${this.labelledInput}
      </div>
    `;
  }

  /**
   * Styles to be included in light DOM, aimed for input pseudoselectors not stylable through ::slotted
   * @return {String} Styles to be included in <style> tag in Light DOM
   */
  get inputInnerStyles() {
    return `
      input#${this._formElementId}::-webkit-inner-spin-button,
      input#${this._formElementId}::-webkit-outer-spin-button {
        -webkit-appearance: none;
        margin: 0;
      }
      input#${this._formElementId}::-webkit-clear-button {
        appearance: none;
        display: none;
      }
      input#${this._formElementId}::-webkit-calendar-picker-indicator {
        position: absolute;
        top: 0;
        left: 0;
        color: transparent;
        background: transparent;
        opacity: 0;
        width: 100%;
        height: 100%;
      }
    `;
  }

  /**
   * Draws date icon
   * @return {TemplateResult} HTML of date icon
   */
  get clearIconBlock() {
    return html`
      <span class="clear">
        <cells-icon
          .icon="${this.clearIcon}"
          .size="${this.clearIconSize}"
          aria-hidden="true"
        ></cells-icon>
      </span>
    `;
  }
}

customElements.define(BbvaFormDate.is, BbvaFormDate);
