import { LitElement, html } from 'lit-element';
import { getComponentSharedStyles } from '@cells-components/cells-lit-helpers/cells-lit-helpers.js';
import '@bbva-web-components/bbva-link';
import '@bbva-web-components/bbva-form-toggle';
import '@cells-components/cells-icon';
import '@cells-components/cells-heading';
import { CellsI18nMixin as cellsI18nMixin } from '@cells-components/cells-i18n-mixin';
import { nothing } from 'lit-html';

import styles from './bbva-list-toggle-styles.js';
/**
A toggle list is commonly used to allow a user to quickly adjust settings within an application.

Example:

Icon
```html
<bbva-list-toggle heading="Accounts" link-text="Modify" toggle-label="Account toggle" main-icon="coronita:account" main-icon-label="accounts" help-icon="coronita:help" help-icon-label="help">
  Lorem ipsum dolor sit amet, consectetur adipiscing elit.
</bbva-list-toggle>
```

No icon
```html
<bbva-list-toggle heading="Accounts" link-text="Modify" toggle-label="Account toggle" help-icon="coronita:help" help-icon-label="help">
  Lorem ipsum dolor sit amet, consectetur adipiscing elit.
</bbva-list-toggle>
```

## Styling
The following custom properties and mixins are available for styling:

### Custom properties

| Selector   | CSS Property  | CSS Variable                       | Theme Variable                    | Foundations/Fallback                                        |
| ---------- | ------------- | ---------------------------------- | --------------------------------- | ----------------------------------------------------------- |
| .link      | margin-top    | --gridSpacerVariant                | --gridSpacer                      | foundations.grid.spacer                       |
| .toggle    | margin-left   | --gridSpacerVariant                | --gridSpacer                      | foundations.grid.spacer                       |
| .main-icon | margin-right  | --gridSpacerVariant                | --gridSpacer                      | foundations.grid.spacer                       |
| .main-icon | color         | --bbva-list-toggle-main-icon-color | --colorsSecondary600              | foundations.colors.secondary600               |
| .heading   | margin-bottom | --gridSpacerVariant                | --gridSpacer                      | foundations.grid.spacer                       |
| .heading   | font-size     |                                    | --typographyTypeMedium            | foundations.typography.typeMedium             |
| .heading   | font-weight   |                                    | --fontFacePrimaryMediumFontWeight | foundations.fontFacePrimary.medium.fontWeight |
| .heading   | font-style    |                                    | --fontFacePrimaryMediumFontStyle  | foundations.fontFacePrimary.medium.fontStyle  |
| .heading   | line-height   |                                    | --lineHeightTypeMedium            | foundations.lineHeight.typeMedium             |
| .heading   | color         | --bbva-list-toggle-heading-color   | --colorsSecondary600              | foundations.colors.secondary600               |
| .help-icon | color         | --bbva-list-toggle-help-icon-color | --colorsPrimaryMedium             | foundations.colors.primaryMedium              |
| .help-icon | margin-left   | --gridSpacerVariant                | --gridSpacer                      | foundations.grid.spacer                       |
| :host      | padding       | --gridSpacerVariant                | --gridSpacer                      | foundations.grid.spacer                       |
| :host      | font-size     |                                    | --typographyTypeSmall             | foundations.typography.typeSmall              |
| :host      | font-weight   |                                    | --fontFacePrimaryBookFontWeight   | foundations.fontFacePrimary.book.fontWeight   |
| :host      | font-style    |                                    | --fontFacePrimaryBookFontStyle    | foundations.fontFacePrimary.book.fontStyle    |
| :host      | line-height   |                                    | --lineHeightTypeSmall             | foundations.lineHeight.typeSmall              |
| :host      | color         | --bbva-list-toggle-color           | --colorsSecondary500              | foundations.colors.secondary500               |

### @apply

| Selector      | Mixins                          |
| ------------- | ------------------------------- |
| .link         | --bbva-list-toggle-link         |
| .toggle       | --bbva-list-toggle-toggle       |
| .main-content | --bbva-list-toggle-main-content |
| .main-icon    | --bbva-list-toggle-main-icon    |
| .heading      | --bbva-list-toggle-heading      |
| .help-icon    | --bbva-list-toggle-help-icon    |
| :host         | --bbva-list-toggle              |
> Styling documentation generated by Cells CLI

* @customElement bbva-list-toggle
* @polymer
* @LitElement
* @demo demo/index.html
*/
export class BbvaListToggle extends cellsI18nMixin(LitElement) {
  static get is() {
    return 'bbva-list-toggle';
  }

  static get properties() {
    return {
      /**
       * `String` to use as text for the link.
       */
      linkText: {
        type: String,
        attribute: 'link-text',
      },
      /**
       * `String` to use as href for the link
       */
      linkHref: {
        type: String,
        attribute: 'link-href',
      },
      /**
       * target attribute for the link
       */
      linkTarget: {
        type: String,
        attribute: 'link-target',
      },
      /**
       * `String` to use as a heading for the component
       */
      heading: {
        type: String,
      },
      /**
       * `Number` to use as heading level for the header
       */
      headingLevel: {
        type: Number,
        attribute: 'heading-level',
      },
      /**
       * `String` string with the iconset:name of the icon to be used. If not
       * set the column will be hidden.
       */
      mainIcon: {
        type: String,
        attribute: 'main-icon',
      },
      /**
       * `String` to be used as a label for the main icon.
       */
      mainIconLabel: {
        type: String,
        attribute: 'main-icon-label',
      },
      /**
       * `String` containing an icon to be used next to the heading. If not
       * set it will be hidden.
       */
      helpIcon: {
        type: String,
        attribute: 'help-icon',
      },
      /**
       * `String` to be used as a label for the help icon.
       */
      helpIconLabel: {
        type: String,
        attribute: 'help-icon-label',
      },
      /**
       * `String` that represents the label to be used for `bbva-form-toggle`
       */
      toggleLabel: {
        type: String,
        attribute: 'toggle-label',
      },
      /**
       * 'Boolean' that exposes the state of the inner bbva-form-toggle
       */
      checked: {
        type: Boolean,
      },
    };
  }

  constructor() {
    super();
    this.headingLevel = 2;
    this.toggleLabel = 'bbva-list-toggle-label';
    this.checked = false;
  }

  static get shadyStyles() {
    return `
      ${styles.cssText}
      ${getComponentSharedStyles('bbva-list-toggle-shared-styles').cssText}
    `;
  }

  _linkClick() {
    /**
     * Fired when the link is clicked
     * @event bbva-list-toggle-link-click
     */
    this.dispatchEvent(
      new CustomEvent('bbva-list-toggle-link-click', {
        bubbles: true,
        composed: true,
      }),
    );
  }

  _toggleClicked(event) {
    event.stopPropagation();

    this.checked = event.detail.checked;

    /**
     * Fired when bbva-form-toggle is clicked
     * @event bbva-list-toggle-change
     */
    this.dispatchEvent(
      new CustomEvent('bbva-list-toggle-change', {
        bubbles: true,
        composed: true,
        detail: {
          checked: this.checked,
        },
      }),
    );
  }

  _helpIconClick() {
    /**
     * Fired when the help icon is clicked
     * @event bbva-list-toggle-help-click
     */
    this.dispatchEvent(
      new CustomEvent('bbva-list-toggle-help-click', {
        bubbles: true,
        composed: true,
      }),
    );
  }

  render() {
    return html`
      <style>
        ${this.constructor.shadyStyles}
      </style>
      ${this.mainIcon
        ? html`
            <span class="sr-only">${this.mainIconLabel}</span>
            <cells-icon .icon="${this.mainIcon}" class="main-icon"></cells-icon>
          `
        : nothing}
      <div class="main-content">
        <div class="heading">
          <cells-heading .level="${this.headingLevel}">${this.heading}</cells-heading>
          ${this.helpIcon
            ? html`
                <bbva-link @click="${this._helpIconClick}"
                  ><span class="sr-only">${this.helpIconLabel}</span>
                  <cells-icon .icon="${this.helpIcon}" class="help-icon"></cells-icon>
                </bbva-link>
              `
            : nothing}
        </div>
        <slot></slot>
        <bbva-link
          @click="${this._linkClick}"
          .href="${this.linkHref}"
          class="link"
          .target=${this.linkTarget}
          >${this.linkText}</bbva-link
        >
      </div>
      <span class="toggle">
        <bbva-form-toggle
          @bbva-form-toggle-change="${this._toggleClicked}"
          ?checked="${this.checked}"
          ><span class="sr-only">${this.t(this.toggleLabel)}</span></bbva-form-toggle
        >
      </span>
    `;
  }
}

customElements.define(BbvaListToggle.is, BbvaListToggle);
