import { LitElement, html, } from 'lit-element';
import { getComponentSharedStyles, } from '@cells-components/cells-lit-helpers/cells-lit-helpers.js';
import { ifDefined } from 'lit-html/directives/if-defined.js';
import { add as GesturesAddListener } from '@cells-components/cells-lit-helpers/utils/gestures.js';
import { CellsFocusVisibleMixin } from '@cells-components/cells-focus-visible-mixin';
import styles from './cells-switch-styles.js';
/**
`CellsSwitch` provides a base class as well as a `<cells-switch>` custom element with switch/toggle functionality as close as possible to ARIA defined ones. It provides an easier and convenient way to build switches with encapsulated HTML & custom styles. As it almost does not provide any styles, it's specially aimed to be extended.

These switches can participate in forms in the same document using standard attributes. For example, using 'required' on a switch will prevent a parent form from being submitted if the switch has not been checked.

When extending from `CellsSwitch`, make sure to:
- Call `CellsSwitch` styles using `super.shadyStyles` if you want to use its basic styling
- Use `this._control` getter in render to enable communication with native forms.

You can overwrite the switch control HTML using the `inputTemplate` template getter. Also, you can overwrite the label HTML using the `labelTemplate` template getter.

**Example**
```js
// Extending CellsSwitch to create a CustomSwitch element
class CustomSwitch extends CellsSwitch {
  static get properties() {
    return {
      // Additional properties here
    };
  }

  static get shadyStyles() { // get styles from upper class using super
    return `
      ${super.shadyStyles}
      ${styles.cssText}
      ${getComponentSharedStyles('custom-switch-shared-styles').cssText}
    `;
  }

  render() { // include any HTML you need; for example, icons
    return html`
      <style>${this.constructor.shadyStyles}</style>
      ...
      ${this._control}
    `;
  }
}

customElements.define('custom-switch', CustomSwitch);
```

```html
<custom-switch name="custom1" value="item1">Checkbox 1</custom-switch>
<custom-switch name="custom2" value="item2" disabled>Checkbox 2</custom-switch>
<custom-switch name="custom3" value="item3">Checkbox 3</custom-switch>
<custom-switch name="custom4" value="item4">Checkbox 4</custom-switch>
```

## Radio state

By default, switches will be treated as on/off controls, and will send their value in forms only when they are checked. You can provide custom tags and values for both off and on states using the properties `tagOn`, `tagOff`, `value` and `valueOff`. You should also set the boolean property `radio` to true to ensure the switch sends its value when it's unchecked and to give it a `button` role instead of a `switch` role. This way, screen readers will read aloud the current active tag instead of the checked state.

## FocusVisible mixin

`CellsSwitch` uses `FocusVisible mixin` from `@cells-components`. This means you can use the 'focus-visible' attribute on your component to manage ':focus' styles based on user navigation interface (keyboard/pointer).

```css
:host([focus-visible]) {
  ... Your focus styles ...
}
```

## Caveats

### 'active' state

You can use the `:active` CSS pseudo-class to define styles for the switch when it's being activated by the user using the mouse. However, this won't work when button is activated using the keyboard. `CellsSwitch` provides an `active` attribute which gets triggered on keydown and keyup with Spacebar/Enter, as well as with down and up mouse actions (touch or pointer).

```css
:host([active]) {
  ... Active styles ...
}
```

* @customElement
* @polymer
* @LitElement
* @appliesMixin CellsFocusVisibleMixin
* @demo demo/index.html
*/
export class CellsSwitch extends CellsFocusVisibleMixin(LitElement) {
  static get is() {
    return 'cells-switch';
  }

  static get properties() {
    return {
      /**
       * If true, switch is disabled
       */
      disabled: {
        type: Boolean,
        reflect: true
      },

      /**
       * If true, switch is required
       */
      required: {
        type: Boolean,
        reflect: true
      },

      /**
       * If true, switch will behave as a pair of radio buttons, and will always be sent in forms regardless of checked state
       */
      radio: {
        type: Boolean
      },

      /**
       * Value for switch
       */
      value: {
        type: String
      },

      /**
       * Value for switch when unchecked (only for radio state)
       */
      valueOff: {
        type: String,
        attribute: 'value-off'
      },

      /**
       * Accessible but not visible label for switch
       */
      label: {
        type: String
      },

      /**
       * Tag to be shown inside switch for checked state
       */
      tagOn: {
        type: String,
        attribute: 'tag-on'
      },

      /**
       * Tag to be shown inside switch for unchecked state
       */
      tagOff: {
        type: String,
        attribute: 'tag-off'
      },

      /**
       * If true, switch is currently checked
       */
      checked: {
        type: Boolean,
        reflect: true
      },

      /**
       * Name of switch related to forms
       */
      name: {
        type: String,
        reflect: true
      },
      /**
       * Associated form for switch
       */
      form: {
        type: String
      },

      _formElementId: {
        type: String
      }

    };
  }

  constructor() {
    super();
    this.checked = false;
    this.value = '';
    this.valueOff = '';
    this._active = false;
    this._formElementId = this.uniqueID();
    this.name = `switch-${this._formElementId}`;
  }

  connectedCallback() {
    super.connectedCallback();
    this._setInitialAttribute('role', this.radio ? 'button' : 'switch');
    if (!this.disabled) {
      this._setInitialAttribute('tabindex', '0');
      this._setInitialAttribute('aria-disabled', 'false');
    }
    this.onClick = this.onClick.bind(this);
    this.addEventListener('click', this.onClick);
    this.onKeyUp = this.onKeyUp.bind(this);
    this.onKeyDown = this.onKeyDown.bind(this);
    this.addEventListener('keyup', this.onKeyUp);
    this.addEventListener('keydown', this.onKeyDown);
    GesturesAddListener(this, 'down', () => !this.disabled && (this.active = true));
    GesturesAddListener(this, 'up', () => this.active = false);
    this.addEventListener('blur', () => this.active = false);
    if (this.hasAttribute('autofocus')) {
      this.focus();
    }
  }

  disconnectedCallback() {
    super.disconnectedCallback();
    this.removeEventListener('click', this.onClick);
    this.removeEventListener('keydown', this.onKeyDown);
    if (this.active) {
      this.active = false;
    }
  }

  firstUpdated(changedProps) {
    if (super.firstUpdated) {
      super.firstUpdated(changedProps);
    }
    this._formElements = this.shadowRoot.querySelector(`.controls`);
    if (this._formElements) {
      this.appendChild(this._formElements);
    }
  }

  updated(changedProps) {
    if (super.updated) {
      super.updated(changedProps);
    }
    if (changedProps.has('checked') || changedProps.has('radio')) {
      if (!this.radio) {
        this.setAttribute('aria-checked', this.checked.toString());
      } else {
        this.removeAttribute('aria-checked');
      }
    }
    if (changedProps.has('radio')) {
      this.setAttribute('role', this.radio ? 'button' : 'switch');
    }
    if (changedProps.has('disabled')) {
      this.setAttribute('aria-disabled', this.disabled.toString());
      this.setAttribute('tabindex', this.disabled ? '-1' : '0');
    }
  }

  /**
   * Active state of switch
   * @return {Boolean} Active state of switch
   */
  get active() {
    return this._active;
  }

  /**
   * Sets active state for switch
   * @param  {Boolean} value Active state for switch
   */
  set active(value) {
    if (value !== this._active) {
      this._active = value;
      if (value) {
        this.setAttribute('active', '');
      } else {
        this.removeAttribute('active');
      }
    }
  }

  /**
   * Returns unique ID string
   * @param  {Number} length Length of ID
   * @return {String}        String of random number
   */
  uniqueID(length = 10) {
    return `_${Math.random().toString(36).substr(2, length)}`;
  }

  static get shadyStyles() {
    return `
      ${styles.cssText}
      ${getComponentSharedStyles('cells-switch-shared-styles').cssText}
    `;
  }

  render() {
    return html`
      <style>${this.constructor.shadyStyles}</style>
      ${this.labelledInput}
      ${this._control}
    `;
  }

  /**
   * HTML structure combining control (inputTemplate) and label (labelTemplate)
   * @return {TemplateResult} HTML of visible control and label
   */
  get labelledInput() {
    return html`
      <div class="container">
        ${this.inputTemplate}
        ${this.labelTemplate}
      </div>
    `;
  }

  /**
   * HTML structure to be used as switch visible control
   * @return {TemplateResult} HTML of visible control
   */
  get inputTemplate() {
    return html`
      <span class="bg" aria-hidden="true">
        <span class="knob">
          <span class="knob-tie"></span>
        </span>
        ${this.tagOff ? html`
          <span class="knob-tag">
            ${this.tagOff}
          </span>
        ` : ''}
        ${this.tagOn ? html`
          <span class="knob-tag">
            ${this.tagOn}
          </span>
        ` : ''}
      </span>
    `;
  }

  /**
   * HTML to be used as label for the switch
   * @return {TemplateResult} HTML of label
   */
  get labelTemplate() {
    return html`
      <span class="label">
        <span class="sr-only" aria-live="${ifDefined(this.radio ? 'assertive' : undefined)}">
          ${this.label} ${this._labelState}
        </span>
        <slot></slot>
      </span>
    `;
  }

  get _labelState() {
    return this.checked ? this.tagOn : this.tagOff;
  }

  get _control() {
    return html`
      <span class="slot">
        <slot name="_control"></slot>
      </span>
      <div class="controls" slot="_control">
        ${!this.radio ? html`
          <input
            id="${this._formElementId}"
            aria-hidden="true"
            tabindex="-1"
            type="checkbox"
            ?disabled="${this.disabled}"
            ?required="${this.required}"
            ?checked="${this.checked}"
            name="${this.name}"
            .value="${this.value}"
            @change="${this._onChange}"
            @input="${this._onInput}"
            form="${ifDefined(this.form)}">
        ` : html`
          <input
            id="${this._formElementId}-off"
            style="display: none"
            aria-hidden="true"
            tabindex="-1"
            type="radio"
            ?disabled="${this.disabled}"
            ?checked="${!this.checked}"
            .checked="${!this.checked}"
            name="${this.name}"
            .value="${this.valueOff}"
            form="${ifDefined(this.form)}">
          <input
            id="${this._formElementId}-on"
            style="display: none"
            aria-hidden="true"
            tabindex="-1"
            type="radio"
            ?disabled="${this.disabled}"
            ?checked="${this.checked}"
            .checked="${this.checked}"
            name="${this.name}"
            .value="${this.value}"
            form="${ifDefined(this.form)}">
        `}
      </div>
    `;
  }

  _setInitialAttribute(attr, value) {
    if (!this.hasAttribute(attr)) {
      this.setAttribute(attr, value);
    }
  }

  _onInput(e) {
    e.preventDefault();
    e.stopPropagation();
  }

  _onChange(e) {
    e.preventDefault();
    e.stopPropagation();
    this.onClick(e);
  }

  _toggle() {
    this.checked = !this.checked;
    this._dispatchEvent('change');
    this._dispatchEvent('input');
  }

  /**
   * Sets checked to true if switch is not disabled
   * @param  {event} e Original click event
   */
  onClick(e) {
    if (this.disabled) {
      e.preventDefault();
      e.stopPropagation();
      e.stopImmediatePropagation();
      return;
    }
    this._toggle();
  }

  /**
   * Checks switch on spacebar or Enter keyup if switch is currently being pressed
   * @param  {event} e Original keyup event
   */
  onKeyUp(e) {
    if ((e.keyCode === 13 || e.keyCode === 32) && this.active) {
      this.active = false;
      this._toggle();
      e.preventDefault();
      e.stopPropagation();
    }
  }

  _dispatchEvent(event) {
    this.dispatchEvent(new CustomEvent(event, {
      bubbles: true
    }));
  }

  /**
   * Sets active state on element on spacebar or Enter keydown
   * @param  {event} e Original keydown event
   */
  onKeyDown(e) {
    const code = e.keyCode;
    if (code === 13 || code === 32) {
      this.active = true;
      e.preventDefault();
      e.stopPropagation();
    }
  }

}

customElements.define(CellsSwitch.is, CellsSwitch);
