import { LitElement, html, } from 'lit-element';
import { getComponentSharedStyles, } from '@cells-components/cells-lit-helpers/cells-lit-helpers.js';
import styles from './cells-modal-styles.js';
import { firstFocusableElement } from '@cells-components/cells-lit-helpers/utils/focus';
import { deepActiveElement } from '@cells-components/cells-lit-helpers/utils/deep-active-element';

/**

This component offers a modal base to be extended or used by other components

Example:

```html
<cells-modal>
  <h1>content</h1>
</cells-modal>


### Custom Properties & Foundations Styles

|          Selector          |    CSS Property     |                   CSS Variable                    |          Theme Variable           |       Foundations/Fallback                                                                                  |
| :------------------------- | :------------------ | :------------------------------------------------ | :-------------------------------- | :---------------------------------------------------------------------------------------------------------- |
| :host                      | background          | --cells-modal-background                          |                                   | transparent                                                                                                 |
| :host                      | display             | --cells-modal-display                             |                                   | none                                                                                                        |
| :host                      | height              | --cells-modal-height                              |                                   | 100%                                                                                                        |
| :host                      | padding             | --cells-modal-padding                             |                                   | env(safe-area-inset-top) env(safe-area-inset-right) env(safe-area-inset-bottom) env(safe-area-inset-left)   |
| :host                      | width               | --cells-modal-width                               |                                   | 100%                                                                                                        |
| :host                      | z-index             | --cells-modal-z-index                             |                                   | 10                                                                                                          |
| .veil                      | background          | --cells-modal-veil-bg-color                       | --colorsSecondary100              | colors.secondary100                                                                                         |

### @apply

|         Selector           |      Mixin                        |
| :------------------------- | :-------------------------------- |
| :host                      | --cells-modal                     |

```
* @customElement
* @polymer
* @LitElement
* @demo demo/index.html
* @extends {LitElement}
*/
export class CellsModal extends LitElement {
  static get is() {
    return 'cells-modal';
  }

  static get properties() {
    return {
      /**
       * Accessibility ID for use with aria-describedby
       */
      accessibleId: {
        type: String,
        attribute: 'accessible-id'
      },
      /**
       * Show/hide modal
       */
      visible: {
        type: Boolean,
        reflect: true,
      },
    };
  }

  constructor() {
    super();

    this.visible = false;
    this.accessibleId = '';
    this._keyupEvent = this._keyupEvent.bind(this);
  }

  connectedCallback() {
    super.connectedCallback();

    this._setInitialAttribute('aria-modal', 'true');
    this._setInitialAttribute('role', 'dialog');
  }

  disconnectedCallback() {
    super.disconnectedCallback();

    document.querySelector('body').style.overflow = '';

    document.removeEventListener('keyup', this._keyupEvent, true);
  }

  firstUpdated(changedProps) {
    this._contentModal = this.shadowRoot.querySelector('.content');
  }

  updated(changedProps) {
    this._setInitialAttribute('aria-describedby', this.accessibleId);

    if (changedProps.has('visible')) {
      document.querySelector('body').style.overflow = this.visible ? 'hidden' : '';

      this._previousFocusedElement = deepActiveElement(document.activeElement);

      /**
       * Emit an event on visible value
       * @event visible-changed
       * @param {Boolean} visible visibility status
       */
      this.dispatchEvent(new CustomEvent('visible-changed', {
        bubbles: true,
        composed: true,
        detail: this.visible,
      }));

      const elem = this.visible
        ? firstFocusableElement(this._contentModal)
        : this._previousFocusedElement;

      window.setTimeout(()=> {
        if (elem) {
          elem.focus();
        }
      }, 17);
    }
  }

  static get shadyStyles() {
    return `
      ${styles.cssText}
      ${getComponentSharedStyles('cells-modal-shared-styles').cssText}
    `;
  }

  get _modalContent() {
    return html``;
  }

  render() {
    return html`
      <style>${this.constructor.shadyStyles}</style>

      <div @focus="${this._catchFocus}" tabindex="0">
        <div class="veil"></div>

        <div class="content">
          <span class="slot-content"><slot></slot></span>

            ${this._modalContent}
        </div>

        <span class="return-focus" tabindex="0" @focus="${this._catchFocus}"></span>
      </div>
    `;
  }

  /**
   * Set the visibility of the modal to true
   */
  open() {
    this.visible = true;
    document.addEventListener('keyup', this._keyupEvent, true);
  }

  /**
   * Set the visibility of the modal to false
   */
  close() {
    this.visible = false;

    this._previousFocusedElement.focus();
    document.removeEventListener('keyup', this._keyupEvent, true);
  }

  _setInitialAttribute(attr, value) {
    if (this.hasAttribute(attr) || value === '') {
      return;
    }

    this.setAttribute(attr, value);
  }

  _keyupEvent(e) {
    if (e.key === 'Esc' || e.key === 'Escape') {
      this.close();
    }

    e.preventDefault();
  }

  _catchFocus() {
    let focusEl = firstFocusableElement(this._contentModal);

    if (focusEl) {
      focusEl.focus();
    }
  }
}

customElements.define(CellsModal.is, CellsModal);
