import { css, unsafeCSS } from 'lit-element';
import * as foundations from '@bbva-web-components/bbva-foundations-styles';

export default css`:host {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  border: none;
  height: var(--bbva-button-icon-size, 3rem);
  width: var(--bbva-button-icon-size, 3rem);
  border-radius: 0;
  background: var(--bbva-button-icon-background, var(--colorsPrimaryMedium, ${unsafeCSS(foundations.colors.primaryMedium)}));
  color: var(--bbva-button-icon-color, var(--colorsPrimaryCoreLightened, ${unsafeCSS(foundations.colors.primaryCoreLightened)}));
  @apply --bbva-button-icon; }

:host(:focus) {
  outline: none; }

:host([focus-visible]),
:host([active]) {
  background: var(--bbva-button-icon-background-focus, var(--colorsPrimaryCoreLight, ${unsafeCSS(foundations.colors.primaryCoreLight)}));
  color: var(--bbva-button-icon-color-focus, var(--colorsPrimaryCoreLightened, ${unsafeCSS(foundations.colors.primaryCoreLightened)}));
  @apply --bbva-button-icon-focus; }

:host([disabled]) {
  background: var(--bbva-button-icon-background-disabled, var(--colorsSecondary200, ${unsafeCSS(foundations.colors.secondary200)}));
  color: var(--bbva-button-icon-color-disabled, var(--colorsSecondary400, ${unsafeCSS(foundations.colors.secondary400)}));
  @apply --bbva-button-icon-disabled; }

:host([hidden]), [hidden] {
  display: none !important; }

*, *:before, *:after {
  box-sizing: inherit; }

span {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }
`;