import { html, } from 'lit-element';
import { CellsModal } from '@cells-components/cells-modal/cells-modal.js';
import { getComponentSharedStyles, } from '@cells-components/cells-lit-helpers/cells-lit-helpers.js';
import { CellsI18nMixin } from '@cells-components/cells-i18n-mixin/cells-i18n-mixin.js';
import styles from './bbva-help-modal-styles.js';
import '@bbva-web-components/bbva-header-main';
import '@bbva-web-components/bbva-button-default';
import '@bbva-web-components/bbva-link';

/**
A help modal provides contextual help information for a specific element. It appears when a user taps on the help icon. A half or full screen modal will appear depending on the situation.

Example:

```html
<bbva-help-modal
  accessibility-text-icon-right1="Close"
  icon-right1="coronita:close"
  header-text="Modal header"
  button-text="Accept"
  link-text="Link">
  <div class="content" slot="slot-content">
    <cells-icon class="content-icon" size="24" icon="coronita:info"></cells-icon>
    <p class="title">Help Modal Title</p>
    <p class="text-content">Lorem ipsum dolor, sit amet consectetur adipisicing elit. Id quod sapiente amet commodi maxime exercitationem, molestias fugit culpa odio obcaecati assumenda libero, consectetur non accusamus eos, quisquam expedita. Impedit, unde!</p>
  </div>
</bbva-help-modal>
```

## Styling
The following custom properties and mixins are available for styling:

### Custom properties

| Selector                                 | CSS Property                | CSS Variable                                        | Theme Variable               | Foundations/Fallback                                  |
| ---------------------------------------- | --------------------------- | --------------------------------------------------- | ---------------------------- | ----------------------------------------------------- |
| :host([sticky]) .modal-footer[sticky]    | box-shadow                  | --bbva-help-modal-footer-sticky-box-shadow          | --boxShadowType2             | foundations.boxShadow.type2                           |
| :host([sticky]) .modal-footer            | transform                   | --bbva-help-modal-footer-sticky-translate           |                              | translateY(100%)                                      |
| .modal-footer .footer-button:first-child | margin-bottom               | --bbva-help-modal-footer-first-button-margin-bottom | --gridSpacerVariant          | 16px                                                  |
| .modal-footer                            | padding                     | --bbva-help-modal-footer-padding                    | --gridSpacerVariant          | 16px 0                                                |
| .modal-footer                            | background-color            | --bbva-help-modal-content-bg-color                  | --colorsPrimaryCoreLightened | foundations.colors.primaryCoreLightened               |
| .modal-content                           | padding                     | --bbva-help-modal-content-padding                   | --gridSpacerVariant          | 24px 16px 0                                           |
| .help-modal                              | background-color            | --bbva-help-modal-content-bg-color                  | --colorsPrimaryCoreLightened | foundations.colors.primaryCoreLightened               |
| :host                                    | --cells-modal-veil-bg-color |                                                     | --colorsPrimaryCoreDarkened  | foundations.colors.primaryCoreDarkened                |
> Styling documentation generated by Cells CLI

* @customElement bbva-help-modal
* @polymer
* @LitElement
* @demo demo/index.html
* @appliesMixin CellsI18nMixin
*/
export class BbvaHelpModal extends CellsI18nMixin(CellsModal) {
  static get is() {
    return 'bbva-help-modal';
  }

  static get properties() {
    return {
     accessibilityTextIconRight1: {
        type: String,
        attribute: 'accessibility-text-icon-right1'
      },
      /**
       * bbva-header-main icon right 1
       */
      iconRight1: {
        type: String,
        attribute: 'icon-right1'
      },
      /**
       * bbva-header-main text
       */
      headerText: {
        type: String,
        attribute: 'header-text'
      },
      /**
       * bbva-header-main type
       */
      headerType: {
        type: String,
        attribute: 'header-type'
      },
      /**
       * Footer button text
       */
      buttonText: {
        type: String,
        attribute: 'button-text'
      },
      /**
       * Footer link text
       */
      linkText: {
        type: String,
        attribute: 'link-text'
      },
      /**
       * Footer link text
       */
      halfModal: {
        type: Boolean,
        attribute: 'half-modal',
        reflect: true
      },
      /**
       * Footer sticky
       */
      _footerSticky: {
        type: Boolean
      },
      /**
       * Sticky animation
       */
      sticky: {
        type: Boolean,
        reflect: true
      }
    };
  }

  constructor() {
    super();

    this.accessibilityTextIconRight1 = '';
    this.iconRight1 = '';
    this.headerText = '';
    this.headerType = 'modal-three-level';
    this.buttonText = '';
    this.linkText = '';
    this.visible = false;
    this.halfModal = false;
    this.sticky =  false;
    this._footerSticky = false;
  }

  firstUpdated(changedProps) {
    /* istanbul ignore next */
    if (super.firstUpdated) {
      super.firstUpdated(changedProps);
    }

    this._modal = this.shadowRoot.querySelector('.help-modal');
    this._header = this.shadowRoot.querySelector('.modal-header');
    this._content = this.shadowRoot.querySelector('.modal-content');
    this._footer = this.shadowRoot.querySelector('.modal-footer');
  }

  updated(changedProps) {
    /* istanbul ignore else */
    if (super.updated) {
      super.updated(changedProps);
    }

    this._calculateContentModalHeight();

    if (this.sticky && this._modal.scrollTop === 0) {
      this._footerSticky = true;
    }
  }

  static get shadyStyles() {
    return `
      ${super.shadyStyles}
      ${styles.cssText}
      ${getComponentSharedStyles('bbva-help-modal-shared-styles').cssText}
    `;
  }

  /**
   * Header HTML structure
   * @return {TemplateResult} HTML of header
   */
  get helpModalHeader() {
    return html`
      <div class="modal-header" ?hidden="${this.halfModal}">
        <slot name="slot-header">
          <bbva-header-main
            class = ${this.headerType}
            accessibility-text-icon-right1 = ${this.accessibilityTextIconRight1}
            icon-right1 = ${this.iconRight1}
            text = ${this.headerText}
            @header-icon-right1-click = ${this.close}></bbva-header-main>
        </slot>
      </div>
    `;
  }

  /**
   * Footer HTML structure
   * @return {TemplateResult} HTML of footer
   */
  get helpModalFooter() {
    return html`
      <div class="modal-footer" ?sticky=${this._footerSticky}>
        <slot name="slot-footer">
          <bbva-button-default id="footer-button" class="footer-button" @click="${this._handleClick}">${this.buttonText}</bbva-button-default>
          <bbva-link id="footer-link" class="footer-button" @click="${this._handleClick}">${this.linkText}</bbva-link>
        </slot>
      </div>
    `;
  }

  /* eslint-disable class-methods-use-this */
  /**
   * Content HTML structure
   * @return {TemplateResult} HTML of content
   */
  get helpModalContent() {
    return html`
      <div class="modal-content">
        <slot name="slot-content"></slot>
      </div>
    `;
  }
  /* eslint-enable class-methods-use-this */

  get _modalContent() {
    return html`
      <div class="help-modal" @scroll="${this._toggleSticky}">
        ${this.helpModalHeader}
        ${this.helpModalContent}
        ${this.helpModalFooter}
      </div>
    `;
  }

  _handleClick(e) {
    /**
     * Emit an event when clicking the footer buttons
     * @event modal-button-click
     * @param {Boolean} e.target
     */
    this.dispatchEvent(new CustomEvent('modal-button-click', {
      bubbles: true,
      composed: true,
      detail: e.target,
    }));

    this.close();
  }

  _calculateContentModalHeight() {
    if (!this.halfModal) {
      const contentHeight = this.querySelector('[slot="slot-content"]').offsetHeight + (this.querySelector('[slot="slot-footer"]')
        ? this.querySelector('[slot="slot-footer"]').offsetHeight
        : this._footer.offsetHeight);

        const windowHeight = window.innerHeight;

      if (contentHeight < (windowHeight / 2)) {
        this.halfModal = true;
        return true;
      }

      this.sticky = contentHeight > windowHeight;
    }

    return false;
  }

  _toggleSticky(event) {
    const scrollValue = event.currentTarget.scrollTop;

    this._footerSticky = scrollValue < this._scrollValue;
    this._scrollValue = scrollValue;

    const modalHeight = (this._header.offsetHeight + this._content.offsetHeight + this._footer.offsetHeight) - this._modal.offsetHeight;

    if (scrollValue >= modalHeight) {
      this.style.setProperty('--bbva-help-modal-footer-sticky-translate', 'translateY(0)');
    } else {
      this.style.setProperty('--bbva-help-modal-footer-sticky-translate', 'translateY(100%)');
    }
  }
}

customElements.define(BbvaHelpModal.is, BbvaHelpModal);
