import { LitElement, html } from 'lit-element';
import { getComponentSharedStyles } from '@cells-components/cells-lit-helpers';
import { CellsI18nMixin as cellsI18nMixin } from '@cells-components/cells-i18n-mixin';
import styles from './bbva-badge-default-styles.js';
/**
`<bbva-badge-default>` Badge text that changes color, default bbva-medium-blue, according to class style.

Available classes:

- `error`.  Color bbva-dark-red.
- `neutral`.  Color bbva-500.
- `success`. Color bbva-dark-green.
- `warning`.  Color bbva-dark-orange.
- `alt`.  Color bbva-dark-aqua

Example:

```html
<bbva-badge-default
  text="Financiado"
  class="success">
</bbva-badge-default>
```

## Styling

The following custom properties and mixins are available for styling:

### Custom Properties & Foundations Styles
| Selector        | CSS Property | CSS Variable                       | Theme Variable                        | Foundations/Fallback                  |
|:----------------|:-------------|:-----------------------------------|:--------------------------------------|:--------------------------------------|
| :host           | font-size    | --bbva-badge-default-font-size     | --typographyTypeSmall                 | typography.typeSmall                  |
| :host           | line-height  | --bbva-badge-default-line-height   | --lineHeightTypeSmall                 | lineHeight.typeSmall                  |
| :host           | font-weight  | --bbva-badge-default-font-weight   | --fontFacePrimaryBookItalicFontWeight | fontFacePrimary.bookItalic.fontWeight |
| :host           | font-style   | --bbva-badge-default-font-style    | --fontFacePrimaryBookItalicFontStyle  | fontFacePrimary.bookItalic.fontStyle  |
| :host           | color        | --bbva-badge-default-color         | --colorsBbvaMediumBlue                | colors.bbvaMediumBlue                 |
| :host(.error)   | color        | --bbva-badge-default-error-color   | --colorsBbvaDarkRed                   | colors.bbvaDarkRed                    |
| :host(.neutral) | color        | --bbva-badge-default-neutral-color | --colorsBbva500                       | colors.bbva500                        |
| :host(.success) | color        | --bbva-badge-default-success-color | --colorsBbvaDarkGreen                 | colors.bbvaDarkGreen                  |
| :host(.warning) | color        | --bbva-badge-default-warning-color | --colorsBbvaDarkOrange                | colors.bbvaDarkOrange                 |
| :host(.alt)     | color        | --bbva-badge-default-alt-color     | --colorsBbvaDarkAqua                  | colors.bbvaDarkAqua                   |

### @apply
| Selector             | Mixins |
|:---------------------|:-------|
| --bbva-badge-default | :host  |

@customElement bbva-badge-default
@polymer
@LitElement
@demo demo/index.html
@appliesMixin CellsI18nMixin
*/
class BbvaBadgeDefault extends cellsI18nMixin(LitElement) {
  static get is() {
    return 'bbva-badge-default';
  }

  static get properties() {
    return {
      /**
       * Text shown in badge
       */
      text: {
        type: String,
      },
    };
  }

  constructor() {
    super();
    this.value = '';
  }

  static get shadyStyles() {
    return `
      ${styles.cssText}
      ${getComponentSharedStyles('bbva-badge-default-shared-styles').cssText}
    `;
  }

  render() {
    return html`
      <style>
        ${this.constructor.shadyStyles}
      </style>
      <span>${this.t(this.text)}</span>
    `;
  }
}

customElements.define(BbvaBadgeDefault.is, BbvaBadgeDefault);
