import { LitElement, html } from 'lit-element';
import { getComponentSharedStyles } from '@cells-components/cells-lit-helpers';
import '@bbva-web-components/bbva-amount/bbva-amount.js';
import styles from './bbva-list-definition-styles.js';

const isAmountValue = value => !!value.amount;
const isTextValue = value => typeof value === 'string';

/**
`<bbva-list-definition>` Shows a list definition element with different views.

Example:

```html
<bbva-list-definition
  items="[[items]]">
</bbva-list-definition>
```

## Styling

The following custom properties and mixins are available for styling:

| Custom Property                         | Description                      | Default                  |
| :-------------------------------------- | :------------------------------- | :----------------------- |
| --bbva-list-definition-direction        | Direction of .list class         | column                   |
| --bbva-list-definition-bg-color         | background applied to :host      | #ffffff                  |
| --bbva-list-definition                  | Mixin applied to :host           | {}                       |
| --bbva-list-definition-list-direction   | `flex-direction` applied to list | column                   |
| --bbva-list-definition-list             | Empty mixin applied to list      | {}                       |
| --bbva-list-definition-key-font-color   | Font color of .value class       | var(--bbva-500, #666666) |
| --bbva-list-definition-key-font-size    | Font size of .value class        | 15px                     |
| --bbva-list-definition-key              | Empty mixin applied to key       | {}                       |
| --bbva-list-definition-value-font-color | Font color of .key class         | var(--bbva-600, #121212) |
| --bbva-list-definition-value-font-size  | Font size of .key class          | 16px                     |
| --bbva-list-definition-value            | Empty mixin applied to value     | {}                       |


@customElement bbva-list-definition
@polymer
@LitElement
@demo demo/index.html
@hero bbva-list-definition.png
*/
class BbvaListDefinition extends LitElement {
  static get is() {
    return 'bbva-list-definition';
  }

  static get properties() {
    return {
      /**
       * Items to show
       */
      items: {
        type: Array,
      },
    };
  }

  static get shadyStyles() {
    return `
      ${styles.cssText}
      ${getComponentSharedStyles('bbva-list-definition-shared-styles').cssText}
      `;
  }

  constructor() {
    super();
    this.items = [];
  }

  render() {
    const list = [];
    this.items.forEach((item, index) => {
      const itemList = html`
        <dl class="list" id="list">
          <dt class="key" id="key_${index}">${item.key}</dt>
          ${isTextValue(item.value)
            ? html`
                <dd class="value" id="value_${index}">${item.value}</dd>
              `
            : ``}
          ${isAmountValue(item.value)
            ? html`
                <dd class="value" id="value_${index}">
                  <bbva-amount
                    class="${item.value.class}"
                    variant="${item.value.variant}"
                    amount="${item.value.amount}"
                    currency-code="${item.value.currencyCode}"
                    local-currency="${item.value.localCurrency}"
                    language="${item.value.language}"
                  >
                  </bbva-amount>
                </dd>
                <dd class="value" id="description_${index}" ?hidden="${!item.value.description}">
                  ${item.value.description}
                </dd>
              `
            : ``}
        </dl>
      `;
      list.push(itemList);
    });

    return html`
      <style>
        ${this.constructor.shadyStyles}
      </style>
      ${list}
    `;
  }
}

customElements.define(BbvaListDefinition.is, BbvaListDefinition);
