import { css, unsafeCSS } from 'lit-element';
import * as foundations from '@bbva-web-components/bbva-foundations-styles';

export default css`:host {
  display: block;
  box-sizing: border-box;
  --_currency-width: 0;
  @apply --bbva-form-amount; }

:host([hidden]), [hidden] {
  display: none !important; }

*, *:before, *:after {
  box-sizing: inherit; }

.slot-input ::slotted(input) {
  padding-left: calc(calc(((2 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 15) * 1em) + var(--_currency-width));
  @apply --bbva-form-amount-input; }

.back {
  position: absolute;
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  padding: calc(((1.5 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 15) * 1em) calc(((2 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 15) * 1em) calc(((1.5 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 15) * 1em);
  color: var(--_field-label-color);
  font-family: inherit;
  font-size: var(--bbva-form-field-input-font-size, var(--typographyTypeSmall, ${unsafeCSS(foundations.typography.typeSmall)}));
  @apply --bbva-form-amount-back; }
  :host([focused]) .back,
  :host([filled]) .back {
    color: var(--_field-input-color);
    padding: calc(((2.5 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 15) * 1em) calc(((2 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 15) * 1em) calc(((0.5 * var(--gridSpacerVariant, var(--gridSpacer, ${unsafeCSS(foundations.grid.spacer)}))) / 15) * 1em);
    @apply --bbva-form-amount-focused-back; }
  :host([readonly]) .back {
    color: var(--_field-readonly-label-color); }
  .back-amount {
    flex: auto;
    opacity: 0;
    @apply --bbva-form-amount-back-amount; }
    :host([focused]) .back-amount,
    :host([filled]) .back-amount {
      flex: none;
      max-width: calc(100% - 3rem);
      @apply --bbva-form-amount-focused-back-amount; }
  .back-currency {
    order: 1;
    @apply --bbva-form-amount-back-currency; }
    .back-currency::before {
      content: "\\00a0"; }
    :host([focused]) .back-currency,
    :host([filled]) .back-currency {
      order: -1;
      @apply --bbva-form-amount-focused-back-currency; }
      :host([focused]) .back-currency::before,
      :host([filled]) .back-currency::before {
        content: ''; }
      :host([focused]) .back-currency.right,
      :host([filled]) .back-currency.right {
        order: 1; }
      :host([focused]) .back-currency:not(.right)::after, :host([focused]) .back-currency.right::before,
      :host([filled]) .back-currency:not(.right)::after,
      :host([filled]) .back-currency.right::before {
        content: "\\00a0"; }
`;