import { css, unsafeCSS } from 'lit-element';
import * as foundations from '@bbva-web-components/bbva-foundations-styles';

export default css`[ambient^=dark] {
  --bbva-form-field-bg-color: #0A5393;
  --bbva-form-field-input-color: var(--colorsPrimaryCoreLightened, ${unsafeCSS(foundations.colors.primaryCoreLightened)});
  --bbva-form-field-readonly-input-color: var(--colorsSecondary300, ${unsafeCSS(foundations.colors.secondary300)});
  --bbva-form-field-label-color: var(--colorsPrimaryCoreLightened, ${unsafeCSS(foundations.colors.primaryCoreLightened)});
  --bbva-form-field-focused-label-color: var(--colorsSecondary100, ${unsafeCSS(foundations.colors.secondary100)});
  --bbva-form-field-readonly-label-color: var(--colorsSecondary300, ${unsafeCSS(foundations.colors.secondary300)});
  --bbva-form-field-border-color: var(--colorsPrimaryCoreLightened, ${unsafeCSS(foundations.colors.primaryCoreLightened)});
  --bbva-form-field-focused-border-color: var(--colorsSecondary100, ${unsafeCSS(foundations.colors.secondary100)});
  --bbva-form-field-clear-bg-color: transparent;
  --bbva-form-field-clear-color: var(--colorsPrimaryCoreLightened, ${unsafeCSS(foundations.colors.primaryCoreLightened)});
  --bbva-form-field-message-color: var(--colorsPrimaryCoreLightened, ${unsafeCSS(foundations.colors.primaryCoreLightened)});
  --bbva-form-field-message-icon-color: var(--colorsPrimaryCoreLightened, ${unsafeCSS(foundations.colors.primaryCoreLightened)});
  --bbva-form-field-info-message-color: var(--colorsPrimaryCoreLightened, ${unsafeCSS(foundations.colors.primaryCoreLightened)});
  --bbva-form-field-info-message-icon-color: var(--colorsPrimaryCoreLightened, ${unsafeCSS(foundations.colors.primaryCoreLightened)});
  --bbva-form-field-invalid-field-bg-color: var(--colorsTertiaryType5Lightened, ${unsafeCSS(foundations.colors.tertiaryType5Lightened)});
  --bbva-form-field-invalid-input-color: var(--colorsSecondary600, ${unsafeCSS(foundations.colors.secondary600)});
  --bbva-form-field-invalid-field-border-color: var(--colorsTertiaryType5Medium, ${unsafeCSS(foundations.colors.tertiaryType5Medium)});
  --bbva-form-field-invalid-label-color: var(--colorsTertiaryType1Dark, ${unsafeCSS(foundations.colors.tertiaryType1Dark)});
  --bbva-form-field-invalid-clear-color: var(--colorsTertiaryType1Dark, ${unsafeCSS(foundations.colors.tertiaryType1Dark)}); }

[ambient=dark150] {
  --bbva-form-field-bg-color: var(--colorsPrimaryMedium, ${unsafeCSS(foundations.colors.primaryMedium)}); }

[ambient=dark300] {
  --bbva-form-field-bg-color: var(--colorsPrimaryCore, ${unsafeCSS(foundations.colors.primaryCore)}); }
`;