import { html } from 'lit-element';
import { getComponentSharedStyles } from '@cells-components/cells-lit-helpers';
import { BbvaFormField } from '@bbva-web-components/bbva-form-field';
import { CellsI18nMixin as cellsI18nMixin } from '@cells-components/cells-i18n-mixin';
import '@bbva-web-components/bbva-button-icon';
import styles from './bbva-form-password-styles.js';

export { bbvaFormFieldAmbient } from '@bbva-web-components/bbva-form-field';
/**
Password fields are used when a user needs to input password information that should be hidden for others to see.

This component extends 'BbvaFormField' to get input field styling & functionality, and exports the same ambients. It defines password type to the input, and replaces the default clear button with a toggle button for showing/hiding the password as text.

**Example:**

```html
<bbva-form-password label="Enter password"></bbva-form-password>
<bbva-form-password label="Enter password" visible></bbva-form-password>
<bbva-form-password label="Enter password" required></bbva-form-password>
<bbva-form-password label="Enter password" value="ExamplePassword"></bbva-form-password>
<bbva-form-password label="Enter password" value="ExamplePassword" readonly></bbva-form-password>
<bbva-form-password label="Enter password" disabled></bbva-form-password>
<bbva-form-password label="Enter password" autofocus></bbva-form-password>
<bbva-form-password
  label="Enter passcode"
  value="1234"
  allowed-pattern="[0-9]"
  auto-validate
  pattern="\d{4}"
  form="formId"
></bbva-form-password>
<bbva-form-password
  label="Enter passcode"
  value="1234"
  allowed-pattern="[0-9]"
  validate-on-blur
  pattern="\d{4}"
></bbva-form-password>
```

## Icons

Since this component uses icons, it will need an [iconset](https://platform.bbva.com/en-us/developers/engines/cells/documentation/cells-architecture/components/components-in-depth/icons) in your project as an application level dependency. In fact, this component uses an iconset in its demo.

## Styling

The following mixins are available for styling, as well as all the custom properties and mixins from `BbvaFormField`:

### @apply

| Selector | Mixin                |
|:---------|:---------------------|
| :host    | --bbva-form-password |

@customElement bbva-form-password
@polymer
@LitElement
@demo demo/index.html
@appliesMixin CellsI18nMixin
*/
export class BbvaFormPassword extends cellsI18nMixin(BbvaFormField) {
  static get is() {
    return 'bbva-form-password';
  }

  static get properties() {
    return {
      /**
       * Icon for toggle button
       */
      toggleIcon: {
        type: String,
        attribute: 'toggle-icon',
      },
      /**
       * Icon for toggle button when password is visible
       */
      toggleVisibleIcon: {
        type: String,
        attribute: 'toggle-visible-icon',
      },
      /**
       * Size for toggle button icon
       */
      toggleIconSize: {
        type: Number,
        attribute: 'toggle-icon-size',
      },

      /**
       * Toggle button accessible text
       */
      toggleAccessibleText: {
        type: String,
        attribute: 'toggle-accessible-text',
      },

      /**
       * Toggle button accessible text when password is visible
       */
      toggleVisibleAccessibleText: {
        type: String,
        attribute: 'toggle-visible-accessible-text',
      },

      /**
       * If true, password is currently visible
       */
      visible: {
        type: Boolean,
      },
    };
  }

  constructor() {
    super();
    this.type = 'password';
    this.toggleIcon = 'coronita:visualize';
    this.toggleVisibleIcon = 'coronita:hide';
    this.toggleIconSize = 24;
    this.toggleAccessibleText = 'bbva-form-password-toggle';
    this.toggleVisibleAccessibleText = 'bbva-form-password-toggle-visible';
    this.visible = false;
  }

  updated(changedProps) {
    super.updated(changedProps);
    if (changedProps.has('visible')) {
      this.type = this.visible ? 'text' : 'password';
    }
  }

  static get shadyStyles() {
    return `
      ${super.shadyStyles}
      ${styles.cssText}
      ${getComponentSharedStyles('bbva-form-password-shared-styles').cssText}
    `;
  }

  /**
   * Overwrites clear button with toggle button
   * @return {TemplateResult} HTML of toggle password button
   */
  get clearButton() {
    return html`
      <bbva-button-icon
        tabindex="-1"
        class="clear"
        .icon="${this.visible ? this.toggleVisibleIcon : this.toggleIcon}"
        .size="${this.toggleIconSize}"
        @click="${this.toggleInput}"
        aria-hidden="true"
        aria-label="${this.visible
          ? this.t(this.toggleVisibleAccessibleText)
          : this.t(this.toggleAccessibleText)}"
      >
      </bbva-button-icon>
    `;
  }

  /**
   * Toggles visible state
   */
  toggleInput() {
    this.visible = !this.visible;
    this.focus();
  }
}

customElements.define(BbvaFormPassword.is, BbvaFormPassword);
